/*
*
* Products
* --------------------------------------------------
*/
.product-characteristics {
  font-weight: 700;
  line-height: 1.55;
  font-size: $font-size-large;

  p + p {
    margin-top: 0;
  }
}

.product-var-1 {

  max-width: 270px;
  margin-right: auto;
  margin-left: auto;

  .box-product-name {
    font-weight: 700;
    font-family: inherit;
    @include link(inherit, $primary);
  }

  .overlay-dark {
    &:hover {
      &:before {
        opacity: .5;
      }
    }
  }

  * + p { margin-top: 22px; }
  * + .box-product-price { margin-top: 5px; }
  * + .button { margin-top: 12px; }
}

.product-var-2 {

  h6 a {
    @include link(inherit, $primary);
  }

  img {
    will-change: transform;
    transition: all 300ms ease-in-out;
  }

  .overlay-dark {
    overflow: hidden;

    &:hover {

      img {
        transform: scale(1.05);
      }

      &:before {
        opacity: .2;
      }
    }
  }

  h6 + p { margin-top: 18px; }
  * + h6 { margin-top: 20px; }
  * + .box-product-price { margin-top: 15px; }
  * + .button { margin-top: 20px; }
}

.box-product-overlay {
  position: relative;
  display: block;
  
  &:before {
    position: absolute;
    content: '';
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    opacity: 0;
    transition: 300ms ease-in-out;
    z-index: 1;
  }
}

.overlay-dark {
  &:before {
    background-color: $gray-base;
  }
}

.product-img-custom {
  position: relative;

  @media (min-width: $screen-lg-min) {
    img {
      position: absolute;
      left: -40px;
      bottom: 0;
      max-width: 106%;
    }
  }

  @media (min-width: $screen-xl-min) {
      img {
        top: -100px;
        max-width: none;
      }
  }
}

/*
*
* Products category
* --------------------------------------------------
*/

.box-product-category {
  @extend %context-dark;

  position: relative;
  display: block;
  text-align: center;

  .box-img {
    height: 280px;
    position: relative;
    overflow: hidden;
    will-change: transform;

    &:before {
      position: absolute;
      content: '';
      top: 0;
      right: -24px;
      bottom: 0;
      left: -24px;
      background-repeat: no-repeat;
      background-size: cover;
      will-change: transform;
      z-index: 1;
    }
  }

  .product-content {
    position: absolute;
    top: 50%;
    left: 50%;
    width: 100%;
    max-width: 280px;
    will-change: transform;
    transform: translate(-50% , -50%);
    z-index: 1;
  }

  h4 {
    font-size: 22px;
    padding-bottom: 1px;
  }

  h6 {
    font-size: 16px;
    font-weight: 400;
  }

  h4 + h6 { margin-top: 16px; }

  @media (min-width: $screen-sm-min) {
    .box-img {
      transform: skewX(-6deg);
      outline: 1px solid transparent;
      -webkit-backface-visibility: hidden;
      margin-left: -1px;

      &:before {
        transform: skewX(6deg);
      }
    }

    .box-img-1,
    .box-img-2 { transform-origin: 100% 100%; }

    .box-img-3,
    .box-img-4 { transform-origin: 0 0; }
  }

  @media (min-width: $screen-md-min) {

    .box-img {
      height: 382px;
    }
  }

  @media (min-width: $screen-lg-min) {

    .box-img {
      height: 280px;
    }

    .box-img {
      transform-origin: 100% 100%;
    }

    .product-content {
      max-width: 250px;
      transform: translate(-43% , -50%);
    }

    &.product-first {
      .product-content {
        transform: translate(-40% , -50%);
      }
    }

    &.product-last {
      .product-content {
        transform: translate(-55% , -50%);
      }
    }
  }

  @media (min-width: $screen-xlg-min) {

    .box-img {
      height: 382px;
    }

    .product-content {
      max-width: 320px;
    }

    h4 {
      font-size: 30px;
      padding-bottom: 2px;
    }
    h6 { font-size: 18px; }
  }
}

.product-category-title {
  position: relative;
  display: inline-block;
  text-transform: uppercase;
  letter-spacing: .1em;
  line-height: 1.94;
  padding-left: 15px;
  padding-right: 15px;

  &:before {
    position: absolute;
    content: '';
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    z-index: -1;
    will-change: transform;
    transform: skewX(-6deg);
    outline: 1px solid transparent;
    -webkit-backface-visibility: hidden;
  }

  @media (min-width: $screen-xlg-min) {
    padding-left: 30px;
    padding-right: 30px;
  }
}

.product-title-primary-sec {
  &:before {
    background: $primary-sec;
  }
}

.product-title-primary {

  &:before {
    background: $primary;
  }
}

* + .box-category-list { margin-top: 30px; }

.box-category-list {

  .list-marked {

    > li + li { margin-top: 6px; }
  }

  ul {
    * + ul { margin-top: 6px; }
  }

  > ul + ul { margin-top: 6px; }
}

/*
*
* Products banner
* --------------------------------------------------
*/

.banner-sale {
  position: relative;
  display: block;
  padding: 20px 15px 30px 15px;

  &.bg-banner-primary {

    &:hover {

      &:before {
        background: darken($primary, 10%)
      }
    }
  }

  &.bg-banner-primary-sec {

    &:before {
      right: -30px;
      transform-origin: 0 0;
      will-change: transform;
    }

    &:hover {

      &:before {
        background: darken($primary-sec, 10%)
      }
    }
  }

  h1 {
    font-size: 46px;
  }
  
  h4 {
    font-size: 22px;
    font-weight: 800;
    letter-spacing: .05em;
    text-transform: uppercase;
  }

  h6 {
    font-size: 16px;
    letter-spacing: .075em;
    text-transform: uppercase;
  }

  @media (min-width: $screen-md-min) {
    padding-top: 37px;
    padding-bottom: 50px;
  }

  @media (min-width: $screen-lg-min) {
    padding-left: 40px;
    padding-right: 20px;

    h1 {
      font-size: 48px;
    }

    &.bg-banner-primary-sec {

      &:before {
        transform: skewX(-6deg);
        outline: 1px solid transparent;
        -webkit-backface-visibility: hidden;
      }
    }
  }

  @media (min-width: $screen-xlg-min) {
    .banner-descr {
      padding-top: 24px;
    }

    h1 {
      font-size: 90px;
    }

    h4 {
      font-size: 30px;
    }

    h6 {
      font-size: 18px;
    }
  }

  @media (min-width: $screen-xl-min) {
    padding-left: 100px;
  }
}

//product rating
.product-rating {
  .icon + .icon {
    margin-left: 3px;
  }
}

.product-rating {

  .icon {
    font-size: 14px;
    line-height: 1;
    color: $primary-sec;
  }

  .inactive {
    color: $gray-light;
  }

  .link {
    font-weight: 700;
    @include link(inherit, $primary);
  }

  * + .link { margin-left: 10px; }
}

* + .product-rating {
  margin-top: 26px;
}

* + .product-item {
  margin-top: 10px;
}

* + .product-social {
  margin-top: 35px;
}

* + .product-price {
  margin-top: 20px;
}

.price-before-sale {
  color: $gray-light;
  text-decoration: line-through;
}

//payment box
.payment-box {
  padding: 20px 15px 20px 45px;
  border: 1px solid $gray-2;
  text-align: left;

  > * {
    vertical-align: middle;
  }
  
  > .form-group,
  > .radio-inline {
    margin-left: -30px;
  }

  .radio-inline {
    font-size: 18px;
    font-weight: 700;
    line-height: 1.2;
    color: $gray-dark;
    font-family: $font-family-sec;
  }

  .link {
    margin-left: 10px;
    font-size: 18px;
    font-weight: 700;
    text-decoration: underline;
    font-family: $font-family-sec;
  }

  & + .button {
    margin-top: 15px;
  }

  .link-img {
    will-change: transform;

    &:hover {
      transform: scale(.9);
    }
  }
  
  * + .group-xs {
    margin-top: 6px;
  }

  * + p {
    margin-top: 5px;
  }

  @media (min-width: $screen-xs-min) {
    padding: 20px 25px 20px 54px;
  }

  @media (min-width: $screen-lg-min) {
    p { max-width: 300px; }
  }
}

.payment-box + .payment-box { border-top: none; }