/*
*
* Responsive unit
* --------------------------------------------------
*/


@include unit-responsive($medias, 20px, 30px);

.unit-spacing-icon {
  @include unit-spacing($medias, 10px, 13px);
}

.unit-spacing-icon-big {
  @include unit-spacing($medias, 16px, 20px);
}

// Unit Spacing
//

.unit-spacing-xs {
  @include unit-spacing($medias, 7px, 15px);
}

.unit-spacing-sm {
  @include unit-spacing($medias, 12px, 16px);
}

.unit-spacing-md {
  @include unit-spacing($medias, 10px, 20px);
}

.unit-spacing-lg {
  @include unit-spacing($medias, 10px, 20px);
}

.unit-spacing-xl {
  @include unit-spacing($medias, 10px, 30px);
}