// Custom button variants
@mixin btn-variant($clr, $clr-hover, $bg-hover, $bg-clr) {
  color: $clr;
  background: transparent;

  span {
    background: $bg-hover;

    &:before {
      background: $bg-clr;
    }
  }

  &:hover,
  &:focus,
  &:active,
  &:hover {
    color: $clr-hover;
  }
}

// Button sizes
@mixin btn-size($padding-vertical, $padding-horizontal, $font-size, $line-height, $border-radius) {
  padding: $padding-vertical $padding-horizontal ($padding-vertical + 4px);
  font-size: $font-size;
  line-height: $line-height;
  border-radius: $border-radius;
}