// Import variables
@import "variables-custom";

// Import mixins
@import "mixins";


/*
*
* Main Styles
* ==================================================
*/

// Remove leading spacing of element
html {
  *:first-child {
    margin-top: 0;
  }

  *:last-child {
    margin-bottom: 0;
  }
}


// Body

body {
  font-family: $font-family-base;
  font-size: $font-size-base;
  font-weight: $font-weight-base;
  line-height: $line-height-base;
  color: $text-color;
  background-color: $body-bg;
}


// Links

a {
  color: $link-color;
  font-family: $font-family-sec;
  text-decoration: none;
  transition: .3s all ease;

  &:hover,
  &:focus {
    color: $link-hover-color;
    text-decoration: $link-hover-decoration;
    outline: none;
  }

  &[href*='callto'],
  &[href*='mailto'] {
    white-space: nowrap;
  }
}

.link-hover{
  color: $link-hover-color;
}

.link-press{
  color: $link-press-color;
}

p {
  margin-bottom: 0;
}

.p a:hover {
  text-decoration: underline;
}


// Images

img {
  display: inline-block;
  max-width: 100%;
  height: auto;
}


/*
*
* Typography
* ==================================================
*/

#{headings()} {
  margin-top: 0;
  margin-bottom: 0;

  font-family: $headings-font-family;
  font-weight: $headings-font-weight;
  color: $headings-color;

  a:hover {
    color: $gray-darker;
  }
}


h1,
.heading-1 {
  font-size: 30px;
  line-height: 1.3;

  @media (min-width: $screen-md-min) {
    line-height: $font-lh-h1;
    font-size: $font-size-h1;
  }
}

h1 {
  .title-date {
    font-size: 30px;
  }
}

h2,
.heading-2 {
  font-size: 26px;
  line-height: 1.5;

  @media (min-width: $screen-md-min) {
    line-height: $font-lh-h2;
    font-size: $font-size-h2;
  }
}

h3,
.heading-3 {
  font-size: 26px;
  line-height: 1.5;
  font-weight: 400;

  @media (min-width: $screen-md-min) {
    line-height: $font-lh-h3;
    font-size: $font-size-h3;
  }
}

h4,
.heading-4 {
  font-size: 22px;
  line-height: 1.5;

  @media (min-width: $screen-md-min) {
    line-height: $font-lh-h4;
    font-size: $font-size-h4;
  }
}

h5,
.heading-5 {
  font-size: 22px;
  line-height: 1.5;
  font-weight: 400;

  @media (min-width: $screen-md-min) {
    line-height: $font-lh-h5;
    font-size: $font-size-h5;
  }
}

h6,
.heading-6 {
  font-size: 16px;
  line-height: 1.5;

  @media (min-width: $screen-md-min) {
    line-height: $font-lh-h6;
    font-size: $font-size-h6;
  }
}

h6 {

  .link-arrow {
    display: inline-block;
    margin-left: 8px;

    &:after {
      font-size: 24px;
      margin-right: 5px;
    }
  }
}

.heading-group {
  h1 + h6 { margin-top: 0; }
}

.heading-group-1 {
  h1 + h6 { margin-top: 25px; }
  h6 + h6 { margin-top: 3px; }
  * + p { margin-top: 15px; }
}


// Big text
.big {
  font-size: 16px;
  line-height: 1.6;

  @media (min-width: $screen-sm-min) {
    font-size: $font-size-large;
  }
}


// Small text
small,
.small {
  display: block;
  font-size: $font-size-small;
}

// Mark
mark {
  color: $white;
  background: $primary;
}

// Divider
// -------------------------

hr{
  margin-top: 0;
  margin-bottom: 0;
  border-top: 1px solid $gray-lighter;
}

.divider {
  margin-top: 0;
  margin-bottom: 0;
  border: none;

  height: 2px;
  width: 60px;
  background-color: $gray-base;
}

/*
*
* Colors
* ==================================================
*/

.page {
  @include text-variant('.text-gray-base', $gray-base, $primary);
  @include text-variant('.text-gray-darker', $gray-darker, $primary);
  @include text-variant('.text-gray-dark', $gray-dark, $primary);
  @include text-variant('.text-gray', $gray, $primary);
  @include text-variant('.text-gray-light', $gray-light, $primary);
  @include text-variant('.text-gray-lighter', $gray-lighter, $primary);
  @include text-variant('.text-primary', $primary, $gray-darker);
  @include text-variant('.text-white', $white, $primary);
}


/*
*
* Main layout
* ==================================================
*/

// Page
//

.page {
  position: relative;
  overflow: hidden;
  min-height: 100vh!important;
}

.header-sm-bottom {
  position: relative;
  z-index: 1080;
}


// Page header
//

.page-header {
  //Kill bootstrap style
  padding: 0;
  margin: 0;
  border-bottom: none;
}


// Page footer
//

.page-footer {

  .footer-bottom-panel {
    padding-top: 30px;
    padding-bottom: 32px;

    p a {
      color: inherit;
      
      &:hover {
        color: $primary;
      }
    }
    
    @media (min-width: $screen-lg-min) {
      position: relative;

      .list-inline {
        position: absolute;
        right: 30px;
      }
    }
  }

  .footer-contacts {
    color: $gray-darker;
    font-weight: 700;

    a {
      font-family: $font-family-base;
    }
  }

  .address {
    max-width: 130px;
    margin-left: auto;
    margin-right: auto;
  }

  .tel {
    a {
      @include link($gray-darker, $primary-sec);
    }
  }

  .mail {
    a {
      @include link($primary-sec, $gray-darker);
    }
  }

  time {
    font-family: $font-family-sec;
  }

  h4 + .post-mini {
    margin-top: 18px;
  }

  h4 + * {
    margin-top: 14px;
  }

  * + h4 {
    margin-top: 52px;
  }

  @media (min-width: $screen-xs-min) {
    .address {
      margin-left: 0;
    }
  }
  
  @media (min-width: $screen-lg-min) {
    .wrap-partners {
      max-width: 170px;
      transform: translateY(-16px);
      margin-bottom: -16px;

      > * {
        display: inline-block;
        margin-top: 16px;
      }
    }  
  }
}

.footer-default {

  a {
    &:active,
    &:focus,
    &:hover {

      color: $white;
    }
  }

  p + p { margin-top: 0; }

  .footer-bottom-panel {
    background: #242424;
  }

  .list-tags {
    li:before {
      background: #403f3f;
    }
  }

  .wrap-bottom-panel {
    @include display-flex();
    @include align-items(center);
    @include justify-content(space-between);
    @include flex-direction(column-reverse);

    p { margin-top: 10px; }

    @media (min-width: $screen-xs-min) {
      @include flex-direction(row);

      p {
        margin-top: 0;
      }
    }
  }
  
  @media (min-width: $screen-md-min) {
    .post-mini {
      .unit__body {
        max-width: 65%;
      }
    }

    .list-tags {
      max-width: 92%;
    }
  }

  @media (min-width: $screen-lg-min) {
    .post-mini {
      .unit__body {
        max-width: 70%;
      }
    }

    .list-tags {
      max-width: 80%;
    }
  }
}

.footer-variant-2 {

  h6 + * {
    margin-top: 18px;
  }

  p + p {
    margin-top: 9px;
  }
}

.footer-variant-3 {

  h6 + * {
    margin-top: 30px;
  }

  .group-lg {
    > * {
      vertical-align: middle;
    }
  }

  @media (min-width: $screen-lg-min) {
    .tel { white-space: nowrap; }

    .group-lg {
      > * {
        vertical-align: top;
      }
    }
  }
}

// Page footer
//

.desktop {
  .modal-custom {
    padding-right: 0 !important;
  }
}

.modal-custom {
  position: fixed;
  top: 0;
  bottom: 0;
  z-index: 9999;
  background: rgba($gray-base, .6);

  .modal-dialog {

    position: absolute;
    left: 50%;
    top: 56px;

    width: 320px;
    margin-left: 0;
    margin-right: 0;
    padding: 20px 0;
  }

  .modal-content {
    padding: 20px 40px 40px 30px;
    border: 0;
    border-radius: 0;
    box-shadow: 0 0 24px rgba(127, 131, 154, 0.42);
  }

  .modal-body {
    padding: 0;
  }

  .close {
    position: absolute;
    top: 10px;
    right: 10px;
    opacity: 1;
    text-shadow: none;
    z-index: 2;
    outline: none;
    transition: 300ms ease all;

    span {

      &:before {
        content: "\f24c";
        font: 400 24px/1 'Material Design Icons';
        display: inline-block;
      }
    }
  }

  .modal-header {
    padding: 0;
    border: none;
  }

  &.fade .modal-dialog {
    transform: translateX(-50%);
  }

  @media (min-height: 600px) {

    .modal-dialog {
      top: 50%;
    }

    &.fade .modal-dialog {
      transform: translate(-50%, -50%);
    }
  }

  @media (min-width: $screen-md-min) {

    .modal-dialog {
      width: 347px;
      margin-right: 0;
    }
  }
}

* + .modal-body {
  margin-top: 30px;
}

.modal-backdrop {
  display: none;
}


// Image pages

.img-shadow {
  box-shadow: 0 0px 7px 0 rgba($gray-base, 0.13);
}

.wrap-img-pages {

  > a {
    display: inline-block;
  }

  > a + a {
    margin-top: 30px;
  }

  @media (min-width: $screen-sm-min) {

    > a + a {
      margin-top: 0;
    }
  }
}

.img-pages-height-1 {

  @media (min-width: $screen-sm-min) {
    min-height: 431px;

    [class*="img-position"] {
      max-width: 345px;
    }
  }

  @media (min-width: $screen-md-min) {
    [class*="img-position"] {
      max-width: 100%;
    }
  }
}

.img-pages-height-2 {

  @media (min-width: $screen-sm-min) {
    min-height: 255px;

    [class*="img-position"] {
      max-width: 530px;
    }
  }

  @media (min-width: $screen-md-min) {
    min-height: 374px;

    [class*="img-position"] {
      max-width: 100%;
    }
  }
}

[class*="img-position"] {

  @media (min-width: $screen-sm-min) {
    position: absolute;
  }
}

@media (min-width: $screen-sm-min) {
  .img-position-1 {
    right: 15px;
    bottom: -56px;
  }

  .img-position-2 {
    left: 50%;
    top: calc(50% + 56px);
    transform: translate(-50%, -50%);
  }

  .img-position-3 {
    top: 0;
    right: 15px;
  }

  .img-position-4 {
    right: 15px;
    bottom: -70px;
  }

  .img-position-5 {
    left: 15px;
    bottom: -70px;
  }
}

@media (min-width: $screen-md-min) {
  .img-position-1 {
    right: auto;
    left: 15px;
  }
}

@media (min-width: $screen-lg-min) {

  .img-position-1 {
    left: -85px;
  }

  .img-position-2 {
    left: auto;
    top: auto;
    right: 15px;
    bottom: -56px;
    transform: translate(0);
  }

  .img-position-3 {
    top: 0;
    right: auto;
    left: 15px;
  }
}

// Animation

@keyframes out {
  from {top: 0;  bottom: 0;}
  to  {top: 100%; bottom: 0;}
}

@keyframes in {
  from {bottom: 100%}
  to  {bottom: 0}
}

// Animation

.desktop .layouts-link {
  display: block;

  img {
    will-change: transform;
    transition: all 300ms ease-in-out;
  }

  &:hover img {
    transform: scale(1.05);
  }
}

// Components & modules & plugins & helpers & fonts
//

.flickr-item {

  > img {
    will-change: transform;
    transition: inherit;
  }

  &:hover {
    img {
      transform: scale(1.05);
    }
  }
}

.soundcloud-player-classic {
  iframe {
    width: 100%;
    border: none;
  }
}

@import "includes";
@import "components/_layout-panel";