/*
* Navbar Default
*/

.rd-navbar-variant-2 {

  .rd-navbar-wrap {
    height: auto !important;
  }

  .rd-navbar {
    position: absolute;
    bottom: 0;
    right: 0;
    left: 0;
    z-index: 4;
  }

  //=============    Static & Fullwidth     ================
  .rd-navbar-static,
  .rd-navbar-fullwidth {

    .rd-navbar-nav {

      > li {

        > a {

          font-size: 16px;
          min-width: 89px;

          @media (min-width: $screen-lg-min) {
            font-size: 18px;
            min-width: 110px;
          }

          @media (min-width: 1400px) {
            min-width: 133px;
          }

          .overlay-skew {
            &:before {
              background: $white;
            }
          }

          &:after {
            top: 22px;
            bottom: 22px;
            border-right: 1px solid rgba($white, .22);
          }

          &:hover {
            color: $primary-sec;
          }
        }

        &:last-child {
          > a {
            &:after {
              border-right: none;
            }
          }
        }

        &.focus,
        &.opened{
          > a{
            color: $primary-sec;
          }
        }

        &.active {
          > a {
            color: $primary-sec;
          }
        }
      }
    }

    .rd-navbar-dropdown {
      > li{

        > a {

          &:hover {
            color: $rd-navbar-dropdown-item-hover-color-2;
          }
        }

        &.focus,
        &.opened {
          > a {
            color: $rd-navbar-dropdown-item-active-color-2;
          }
        }
      }
    }

    .rd-navbar-megamenu {

      > li {

        // Megamenu column
        > ul {

          // Megamenu link
          a {
            &:hover {
              color: $rd-navbar-megamenu-item-hover-color-2;
            }
          }
        }
      }
    }

    // RD Navbar Inner
    .rd-navbar-inner {
      max-width: $rd-navbar-width;
      padding-left: 15px;
      padding-right: 15px;
      background: rgba($primary-sec, .9);

      @media (min-width: 1400px) {
        padding-left: 35px;
        padding-right: 35px;
      }
    }

    .rd-navbar-panel {
      display: none;
    }

    // RD Navbar top panel
    .rd-navbar-top-panel{

      &-inner{
        @include display-flex();
        @include align-items(center);
        @include justify-content(flex-end);
        font-size: 14px;
      }
    }

    .rd-navbar-aside-wrap {
      @include display-flex();
      @include align-items(center);
      @include justify-content(space-between);
      width: 100%;
      max-width: 1460px;
    }

    &.rd-navbar--is-stuck,
    &.rd-navbar--is-clone{
      position: fixed;
      bottom: auto;

      .rd-navbar-inner {
        background: $primary-sec;
      }
    }

    .rd-navbar-search {
      .rd-navbar-search-toggle {
        color: $white;

        &:hover {

          .search-content {
            text-decoration: underline;
          }
        }

        span {
          left: 2px;
        }
      }

      .rd-search-form-submit{

        &:hover {
          color: $primary-sec;
        }
      }
    }

    .rd-navbar-link-inline {

      a {

        &:hover {
          text-decoration: underline;
        }
      }

      > * + * {
        margin-left: 27px;
      }

      .forms-modal {

        > * + * {
          margin-left: 27px;
        }

        > a {
          position: relative;
          width: auto;

          &:before {
            position: absolute;
            content: '/';
            top: 50%;
            left: -17px;
            transform: translateY(-50%);
          }
        }
      }
    }

    .search_title {
      a {
        @include link($gray-darker, $primary-sec);
      }
    }

    .search_all {

      a {
        color: $gray-darker;
      }
    }

    .search {
      background: $primary-sec;
    }

    @media (min-width: 1920px) {

      .rd-navbar-aside-wrap {
        max-width: 100%;

        &:before {
          content: '';
          width: 212px;
        }
      }
    }
  }

  .rd-navbar-static {

    .logo-long {
      display: block;
    }

    .logo-short {
      display: none;
    }

    .rd-navbar-inner {
      @include justify-content(flex-end);
    }
  }

  .rd-navbar-fullwidth {

    .logo-long {
      display: none;
    }

    .logo-short {
      display: block;
    }

    .rd-navbar-aside-right {
      margin-top: 0;
    }
  }

  // Fixed Layout

  .rd-navbar-fixed {

    .rd-navbar-nav {

      li {
        > a {
          color: $rd-navbar-fixed-nav-color;
        }

        &:hover,
        &.active,
        &.opened{
          > a{
            color: $rd-navbar-fixed-nav-active-color;
            background: $rd-navbar-fixed-nav-active-background-2;
          }
          > .rd-navbar-submenu-toggle {
            color: $rd-navbar-fixed-nav-active-color;
          }
        }
      }
    }

    .rd-navbar-panel {

      &:before {
        background: $rd-navbar-fixed-nav-background-2;
      }
    }

    .rd-navbar-search{
      .rd-search-form-submit{

        &:hover {
          color: $primary-sec;
        }
      }
    }

    .logo-long { display: none; }

    .forms-modal {
      a {
        color: $gray-darker;

        &:hover {
          color: $primary-sec;
        }
      }
    }
  }

  // Scrollbar styles
  .rd-navbar-fixed .rd-navbar-nav-wrap,
  .rd-navbar-sidebar .rd-navbar-sidebar-inner  {
    &::-webkit-scrollbar-thumb {
      background: lighten($primary-sec, 20%);
    }

    &::-webkit-scrollbar-track {
      background: $primary-sec;
    }
  }

  .rd-megamenu-header {
    color: $primary-sec;
  }

  .modal-custom {

    .close {

      span {
        color: $primary-sec;
      }

      &:hover {
        span {
          color: $gray-darker;
        }
      }
    }
  }
}

.rd-navbar-link-inline {

  > * {
    display: inline-block;
    vertical-align: middle;
  }

  .forms-modal > a {
    color: $white;
  }

  .forms-modal {
    > a {
      display: inline-block;
      width: 100%;

      & + & {
        margin-top: 10px;
      }
    }
  }
}