/*
*
* Sections
* --------------------------------------------------
*/


// Section Spacing
// -------------------------

.section-partners {
  padding: 15px 15px;
}

.section-xxs {
  padding-top: 43px;
  padding-bottom: 43px;
}

.section-xs {
  padding-top: 56px;
  padding-bottom: 56px;
}

.section-sm {
  padding-top: 65px;
  padding-bottom: 65px;
}

.section-md {
  padding-top: 70px;
  padding-bottom: 70px;
}

.section-lg {
  padding-top: 56px;
  padding-bottom: 56px;
}

.section-inset-custom {
  padding-top: 1px;
  padding-bottom: 1px;
}

// Section Styles
// -------------------------

@media (min-width: $screen-sm-min) {
  .section-xs {

    &.inset-vertical-0 {
      padding-top: 0;
      padding-bottom: 0;
    }

    &.inset-sm-bottom-0 {
      padding-bottom: 0;
    }

    &.inset-sm-bottom-15 {
      padding-bottom: 15px;
    }

    &.inset-sm-bottom-90 {
      padding-bottom: 90px;
    }
  }
}

@media (min-width: $screen-md-min) {
  .section-lg {
    padding-top: 120px;
    padding-bottom: 120px;
  }
}

.section-relative {
  position: relative;
  z-index: 1;
}

.section-overflow {

  @media (min-width: $screen-sm-min) {
    overflow: hidden;
  }
}

.page-title + .section-border-top { border-top: 1px solid $white; }

.section-video-skew {
  position: relative;
  z-index: 3;
}

// Section Grid Demonstration
.section-grid-demonstration {
  @media (max-width: $screen-xs-min) {
    [class^="col"] {
      padding: 5px;
    }
  }
  .grid-demonstration-item {
    background: $gray-lighter;

    @media (min-width: $screen-sm-min) {
      padding: 35px 30px;
      text-align: left;
    }

    @media (max-width: $screen-md-max) {
      h3 {
        font-size: 16px;
        line-height: 16px;
      }
      p {
        white-space: nowrap;
        overflow: hidden;
        text-overflow: ellipsis;
        width: 100%;
      }
    }
  }

  .divider + .row {
    margin-top: 35px;
  }
}