//
// Page title
// --------------------------------------------------
.page-title {
  position: relative;
  z-index: 1;
  text-align: left;

  -webkit-background-size: cover;
  background-size: cover;
  background-position: center 50%;
  background-color: #1b181d;
}

.page-title-inset-1 {
  padding: 40px 0;
}

.page-title-inset-2 {
  padding: 40px 0;
}

.breadcrumbs-elements {
  background-image: url(../images/breadcrumbs-elements.jpg);
}

.breadcrumbs-products {
  background-image: url(../images/breadcrumbs-products.jpg);
}

.breadcrumbs-videos {
  background-image: url(../images/breadcrumbs-videos.jpg);
}

.breadcrumbs-blog {
  background-image: url(../images/breadcrumbs-blog.jpg);
}

.page-title-text {
  font-family: $headings-font-family;
  font-weight: $headings-font-weight;
  font-size: 28px;
  line-height: 1.2;
  letter-spacing: 0;
  color: $white;
}

.page-title__overlay {
  opacity: .90;
  padding: 30px 80px 30px 20px;
  background: $gray-darker;
}

@media (min-width: $screen-sm-min) {

  .page-title-inset-1 {
    padding-top: 70px;
    padding-bottom: 70px;
  }

  .page-title-inset-2 {
    padding-top: 70px;
    padding-bottom: 70px;
  }

  .page-title-text {
    font-size: 48px;
  }

  .page-title__overlay {
    padding: 30px 150px 50px 60px;
  }
}

@media (min-width: $screen-md-min) {

  .page-title-inset-2 {
    padding-top: 110px;
    padding-bottom: 110px;
  }
}

.desktop {
  
  @media (min-width: $screen-md-min) {

    .page-title-inset-1 {
      padding-top: 190px;
    }

    .page-title-inset-2 {
      padding-top: 230px;
    }
  }
}
