/*
*
* Counter
* --------------------------------------------------
*/


.counter {

  &__subtitle{}

  &-wrap{}
}

.counter-wrap {
  padding: 8px 30px 20px;
  font-weight: 700;
  font-family: $font-family-sec;
  color: $white;

  > div {
    font-size: 90px;
    line-height: 1;
  }
  
  > p {
    font-size: 18px;
    letter-spacing: .025em;
  }

  * + p {
    margin-top: -6px;
  }
  
  @media (min-width: $screen-xs-min) {
    text-align: left;
  }
}

// Box counter
.box-counter {
  color: $white;
  line-height: 1;
  font-family: $font-family-sec;

  > div,
  .counter {
    font-size: 45px;
    font-weight: 700;
  }

  * + p { margin-top: 6px; }
}

.box-counter-title {
  font-size: 20px;
  color: $white;
}

@media (min-width: $screen-md-min) {
  .box-counter {

    > div,
    .counter {
      font-size: 70px;
    }
  }

  .box-counter-title {
    font-size: 26px;
  }
}

@media (min-width: $screen-lg-min) {
  .box-counter {
    padding: 10px 15px 60px;

    > div,
    .counter {
      font-size: 90px;
    }
  }

  .box-counter-title {
    font-size: 30px;
  }
}

.counter-k {
  &:after {
    content: 'k';
  }
}

.counter-percent {
  &:after {
    content: '%';
  }
}

.counter-plus {
  &:after {
    content: '+';
  }
}