/*
*
* Flex Grid system
* --------------------------------------------------
*/



// Base Grid Styles
// --------------------------------------------------

.shell,
.shell-wide,
.shell-fluid{
  @include make-shell($cell-spacing);
}

.shell,
.shell-wide{
  min-width: $shell-width;
  max-width: $shell-xs-width;

  @media (max-width: $screen-xs-max) {
    &.shell-xs-justify {
      max-width: 100%;
    }   
  }

  @media (min-width: $screen-sm-min) {
    max-width: $shell-sm-width;
  }

  @media (min-width: $screen-md-min) {
    max-width: $shell-md-width;
  }

  @media (min-width: $screen-lg-min) {
    max-width: $shell-lg-width;
  }
}

.shell-wide {
  @media (min-width: $screen-lg-min) {
    max-width: $shell-xl-width;
  }
}

.shell-fluid-inset-0 {
  padding-left: 0;
  padding-right: 0;
}

.shell-inset-xs-15 {
  padding-left: 0;
  padding-right: 0;

  @media (min-width: $screen-xs-min) {
    padding-left: 15px;
    padding-right: 15px;
  }
}

.shell-inset-sm-15 {
  padding-left: 0;
  padding-right: 0;

  @media (min-width: $screen-sm-min) {
    padding-left: 15px;
    padding-right: 15px;
  }
}

@media (min-width: $screen-md-min) {

  .shell-fluid-inset-md-65 {
    padding-left: 65px;
    padding-right: 65px;
  }
}

@media (min-width: $screen-xs-min) and (max-width: 1300px) {
  .shell-inset-xs-65 {
    padding-left: 65px;
    padding-right: 65px;
  }
}

@media (min-width: $screen-lg-min) {
  .shell-fluid-inset-lg-50 {
    padding-left: 50px;
    padding-right: 50px;
  }
}

@media (min-width: $screen-xlg-min) {

  .shell-offset-left-xlg-50 {
    margin-left: 50px;
  }

  .shell-offset-left-xlg-65 {
    margin-left: 65px;
  }
}

.shell-fluid-custom {

  @media (min-width: $screen-xlg-min) {
    padding-left: 50px;
    padding-right: 50px;
  }

  @media (min-width: $screen-xl-min) {
    padding-left: 75px;
    padding-right: 75px;
  }
}


.range {
  @include make-range($cell-spacing);

  > .range{
    margin-left: 0;
    margin-right: 0;
  }

  &-center {
    @include justify-content(center);
  }

  &-left {
    @include justify-content(flex-start);
  }

  &-right {
    @include justify-content(flex-end);
  }

  &-justify {
    @include justify-content(space-between);
  }

  &-around {
    @include justify-content(space-around);
  }

  &-top {
    @include align-items(flex-start);
  }

  &-reverse {
    @include flex-direction(row-reverse);
  }

  &-middle {
    @include align-items(center);
  }

  &-bottom {
    @include align-items(flex-end);
  }
}

.range-spacer{
  @include flex-basis(100%);
}

[class*="cell-"] {
  @include make-cell-spacing($cell-spacing);
}

// Create IE 9 and below degradation styles
html.lt-ie-10 * + .range,
* + .range {
  margin-top: $range-ws;
}

html.lt-ie-10 * + [class*='cell-'],
* + [class*='cell-'],
html.lt-ie-10 * + .range-sm,
* + .range-sm {
  margin-top: $range-ws-sm;
}

html.lt-ie-10 * + .range-lg,
* + .range-lg {
  margin-top: $range-ws-lg;
}

html.lt-ie-10 .range-condensed,
.range-condensed {
  margin-left: 0;
  margin-right: 0;

  > [class*='cell'] {
    padding-left: 0;
    padding-right: 0;
  }

  > *  + [class*='cell'] {
    margin-top: 0;
  }
}

html.lt-ie-10 .range-negative-offset,
.range-negative-offset {

  @media (min-width: $screen-sm-min) {
    margin-left: -68px;
    margin-right: -68px;
  }

  @media (min-width: $screen-lg-min) {
    margin-left: -34px;
    margin-right: -34px;
  }
}

html.lt-ie-10 .range-negative-offset-2,
.range-negative-offset-2 {

  @media (min-width: $screen-lg-min) {
    margin-left: -110px;
    margin-right: -58px;

    .cell-negative-offset {
      margin-right: -58px;
    }
  }
}

html.lt-ie-10 .range-negative-offset-3,
.range-negative-offset-3 {

  @media (min-width: $screen-sm-min) {
    margin-left: -36px;
    margin-right: -36px;
  }

  @media (min-width: $screen-md-min) {
    margin-left: -26px;
    margin-right: -26px;
  }
}

html.lt-ie-10 .range-negative-offset-4,
.range-negative-offset-4 {

  @media (min-width: $screen-sm-min) {
    margin-left: -45px;
    margin-right: -45px;
  }

  @media (min-width: $screen-lg-min) {
    margin-left: -40px;
    margin-right: -40px;
  }
}

html.lt-ie-10 .range-negative-offset-5,
.range-negative-offset-5 {

  @media (min-width: $screen-lg-min) {
    margin-right: -60px;
    margin-left: -22px;

    .cell-negative-offset {
      margin-left: -60px;
    }
  }

  @media (min-width: $screen-xlg-min) {
    margin-left: -13px;
  }
}

html.lt-ie-10 .range-negative-offset-6,
.range-negative-offset-6 {

  @media (min-width: $screen-lg-min) {
    margin-left: -60px;
    margin-right: -22px;

    .cell-negative-offset {
      margin-right: -60px;
    }
  }

  @media (min-width: $screen-xlg-min) {
    margin-right: -13px;
  }
}

html.lt-ie-10 .range-narrow,
.range-narrow {
  @include make-range($cell-spacing-narrow);

  > [class*='cell'] {
    @include make-cell-spacing($cell-spacing-narrow);
  }

  > *  + [class*='cell'] {
    @include make-cell-spacing($cell-spacing-narrow);
  }
}


// Flex Grid Styles
// --------------------------------------------------

.range {
  @include display-flex();
  @include flex(0 1 auto);
  @include flex-direction(row);
  @include flex-wrap(wrap);

  > .range{
    @include flex-basis(100%);
  }
}

.range-vertical{
  @include flex-direction(column);
}

.range > [class*='cell'] {
  @include flex(0 0 auto);
  @include flex-basis(100%);
}

@each $resolution, $alias in ($screen-xs-min : xs, $screen-sm-min : sm, $screen-md-min : md, $screen-lg-min : lg, $screen-xlg-min : xlg, $screen-xl-min : xl) {
  @media (min-width: $resolution) {
    * + [class*='cell-#{$alias}-'] {
      margin-top: 0;
    }

    @include make-grid-system($alias, $cell-count);
  }
}

html.lt-ie-10 {
  .range > {
    @include make-fallback($cell-count);
  }
}