/*
*
* Swiper
* --------------------------------------------------
*/


.swiper-container {
  min-height: 450px;
  margin: 0 auto;
  position: relative;
  overflow: hidden;
  /* Fix of Webkit flickering */
  z-index: 1;

  @media (max-height: 560px) {
    min-height: 520px;
  }

  h6 {
    font-weight: 400;
  }

  * + .button {
    margin-top: 20px;
  }

  @media (min-width: $screen-sm-min) {
    * + .button {
      margin-top: 40px;
    }
  }
}

.swiper-var-1 {
  height: calc(100vh - 56px);

  .swiper-slide {
    padding-top: 30px;
    padding-bottom: 67px;
    background-position-x: 70%;

    @extend %context-dark;
  }

  .swiper-button-next,
  .swiper-button-prev {
    display: none;
  }

  @media (min-width: $screen-xs-min) {
    height: 500px;

    .swiper-slide {
      padding-top: 67px;
    }
  }

  @media (min-width: $screen-sm-min) {
    height: 500px;

    .swiper-slide {
      background-position-x: 50%;
    }
  }

  @media (min-width: $screen-md-min) {
    height: 700px;

    .swiper-slide {
      @include align-items(flex-end);
      padding-top: 140px;
    }
  }

  @media (min-width: $screen-lg-min) {
    height: 720px;
  }

  @media (min-width: 1400px) {
    height: 779px;
  }
}

.swiper-var-2 {
  height: calc(100vh - 56px);

  .swiper-slide {
    padding-top: 30px;
    padding-bottom: 67px;
    background-position-x: 70%;
  }

  .box-overlay-white {
    position: relative;
    padding: 30px 15px;

    &:before {
      position: absolute;
      content: '';
      top: 0;
      right: 0;
      bottom: 0;
      left: 0;
      background: rgba($white, .8);
      z-index: -1;
    }
  }

  .swiper-pagination {
    display: none;
  }

  .swiper-brand {
    display: none;
    position: absolute;
    top: 60px;
    width: 100%;
    z-index: 1;
  }

  @media (min-width: $screen-xs-min) {
    height: 500px;
  }

  @media (min-width: $screen-sm-min) {

    .swiper-slide {
      padding-top: 67px;
      background-position-x: 100%;
    }

    .box-overlay-white {
      padding: 30px;
    }
  }

  @media (min-width: $screen-md-min) {
    height: 679px;

    .swiper-slide {
      @include align-items(center);
      padding-top: 133px;
      padding-bottom: 170px;
      background-position-x: 80%;
    }

    .swiper-brand {
      display: block;
    }
  }

  @media (min-width: $screen-lg-min) {

    .swiper-slide {
      background-position-x: 50%;
    }

    .box-overlay-white {

      &:before {
        display: none;
      }
    }
  }

  [class*='text-width'] {
    margin-left: auto;
    margin-right: auto;
  }
}

.swiper-var-3 {
  height: calc(100vh - 56px);

  .swiper-slide {
    padding-top: 30px;
    padding-bottom: 67px;
    background-position-x: 70%;
  }

  .swiper-button-next,
  .swiper-button-prev {
    display: none;
  }

  .box-overlay-white {
    position: relative;
    padding: 30px 15px;

    &:before {
      position: absolute;
      content: '';
      top: 0;
      right: 0;
      bottom: 0;
      left: 0;
      background: rgba($white, .8);
      z-index: -1;
    }
  }

  @media (min-width: $screen-xs-min) {
    height: 500px;

    .swiper-slide {
      padding-top: 67px;
    }
  }

  @media (min-width: $screen-md-min) {
    height: 650px;

    .swiper-wrapper {
      height: calc(100% - 140px);
    }

    .swiper-slide {
      background-position-x: 10%;
    }

    .box-overlay-white {
      padding-left: 0;
      padding-right: 0;
    }

    .swiper-pagination {
      position: static;

      .swiper-pagination-bullet {
        position: relative;
        width: 25%;
        height: 140px;
        border-radius: 0;
        background-size: cover;
        background-repeat: no-repeat;
        background-position: 50%;

        &:before {
          position: absolute;
          content: '';
          top: 50px;
          right: 50px;
          bottom: 50px;
          left: 50px;
          background-color: rgba($gray-base, .4);
          opacity: 0;
          overflow: hidden;
          transition: all .3s ease-in-out;
        }

        &:hover {
          &:before {
            top: 0;
            right: 0;
            bottom: 0;
            left: 0;
            opacity: 1;
            overflow: visible;
          }
        }

        &:nth-child(1) {
          background-image: url(../images/swiper3-slide1-mini.jpg);
        }

        &:nth-child(2) {
          background-image: url(../images/swiper3-slide2-mini.jpg);
        }

        &:nth-child(3) {
          background-image: url(../images/swiper3-slide3-mini.jpg);
        }

        &:nth-child(4) {
          background-image: url(../images/swiper3-slide4-mini.jpg);
        }
      }
    }

    &.swiper-container-horizontal {
      > .swiper-pagination {

        .swiper-pagination-bullet {
          margin: 0;
          padding: 0;
        }
      }
    }

    .box-overlay-white {

      &:before {
        display: none;
      }
    }
  }

  @media (min-width: $screen-lg-min) {
    height: 750px;

    .swiper-wrapper {
      height: calc(100% - 204px);
    }

    .swiper-pagination {

      .swiper-pagination-bullet {
        height: 204px;
      }
    }
  }

  @media (min-width: 1400px) {
    height: 852px;

    .swiper-slide {
      background-position-x: 50%;
    }
  }
}


.swiper-container-no-flexbox .swiper-slide {
  float: left;
}

.swiper-container-vertical > .swiper-wrapper {
  @include flex-direction(column);
}

.swiper-wrapper {
  position: relative;
  width: 100%;
  height: 100%;
  z-index: 1;
  @include display-flex();
  transition-property: transform;
  box-sizing: content-box;
}

.swiper-container-android .swiper-slide,
.swiper-wrapper {
  transform: translate3d(0px, 0, 0);
}

.swiper-container-multirow > .swiper-wrapper {
  flex-wrap: wrap;
}

.swiper-container-free-mode > .swiper-wrapper {
  transition-timing-function: ease-out;
  margin: 0 auto;
}

/* a11y */
.swiper-container .swiper-notification {
  position: absolute;
  left: 0;
  top: 0;
  pointer-events: none;
  opacity: 0;
  z-index: -1000;
}

/* IE10 Windows Phone 8 Fixes */
.swiper-wp8-horizontal {
  touch-action: pan-y;
}

.swiper-wp8-vertical {
  touch-action: pan-x;
}


/* Coverflow */
.swiper-container-coverflow .swiper-wrapper {
  /* Windows 8 IE 10 fix */
}


// Swiper fade effect
//--------------------------------------------------

.swiper-container-fade.swiper-container-free-mode .swiper-slide {
  transition-timing-function: ease-out;
}

.swiper-container-fade .swiper-slide {
  pointer-events: none;
}

.swiper-container-fade .swiper-slide .swiper-slide {
  pointer-events: none;
}

.swiper-container-fade .swiper-slide-active,
.swiper-container-fade .swiper-slide-active .swiper-slide-active {
  pointer-events: auto;
}


// Swiper preloader
//--------------------------------------------------

.swiper-lazy-preloader {
  width: 42px;
  height: 42px;
  position: absolute;
  left: 50%;
  top: 50%;
  margin-left: -21px;
  margin-top: -21px;
  z-index: 10;
  transform-origin: 50%;
  animation: swiper-preloader-spin 1s steps(12, end) infinite;
}

.swiper-lazy-preloader:after {
  display: block;
  content: "";
  width: 100%;
  height: 100%;
  background-image: url("data:image/svg+xml;charset=utf-8,%3Csvg%20viewBox%3D'0%200%20120%20120'%20xmlns%3D'http%3A%2F%2Fwww.w3.org%2F2000%2Fsvg'%20xmlns%3Axlink%3D'http%3A%2F%2Fwww.w3.org%2F1999%2Fxlink'%3E%3Cdefs%3E%3Cline%20id%3D'l'%20x1%3D'60'%20x2%3D'60'%20y1%3D'7'%20y2%3D'27'%20stroke%3D'%236c6c6c'%20stroke-width%3D'11'%20stroke-linecap%3D'round'%2F%3E%3C%2Fdefs%3E%3Cg%3E%3Cuse%20xlink%3Ahref%3D'%23l'%20opacity%3D'.27'%2F%3E%3Cuse%20xlink%3Ahref%3D'%23l'%20opacity%3D'.27'%20transform%3D'rotate(30%2060%2C60)'%2F%3E%3Cuse%20xlink%3Ahref%3D'%23l'%20opacity%3D'.27'%20transform%3D'rotate(60%2060%2C60)'%2F%3E%3Cuse%20xlink%3Ahref%3D'%23l'%20opacity%3D'.27'%20transform%3D'rotate(90%2060%2C60)'%2F%3E%3Cuse%20xlink%3Ahref%3D'%23l'%20opacity%3D'.27'%20transform%3D'rotate(120%2060%2C60)'%2F%3E%3Cuse%20xlink%3Ahref%3D'%23l'%20opacity%3D'.27'%20transform%3D'rotate(150%2060%2C60)'%2F%3E%3Cuse%20xlink%3Ahref%3D'%23l'%20opacity%3D'.37'%20transform%3D'rotate(180%2060%2C60)'%2F%3E%3Cuse%20xlink%3Ahref%3D'%23l'%20opacity%3D'.46'%20transform%3D'rotate(210%2060%2C60)'%2F%3E%3Cuse%20xlink%3Ahref%3D'%23l'%20opacity%3D'.56'%20transform%3D'rotate(240%2060%2C60)'%2F%3E%3Cuse%20xlink%3Ahref%3D'%23l'%20opacity%3D'.66'%20transform%3D'rotate(270%2060%2C60)'%2F%3E%3Cuse%20xlink%3Ahref%3D'%23l'%20opacity%3D'.75'%20transform%3D'rotate(300%2060%2C60)'%2F%3E%3Cuse%20xlink%3Ahref%3D'%23l'%20opacity%3D'.85'%20transform%3D'rotate(330%2060%2C60)'%2F%3E%3C%2Fg%3E%3C%2Fsvg%3E");
  background-position: 50%;
  background-size: 100%;
  background-repeat: no-repeat;
}

.swiper-lazy-preloader-white:after {
  background-image: url("data:image/svg+xml;charset=utf-8,%3Csvg%20viewBox%3D'0%200%20120%20120'%20xmlns%3D'http%3A%2F%2Fwww.w3.org%2F2000%2Fsvg'%20xmlns%3Axlink%3D'http%3A%2F%2Fwww.w3.org%2F1999%2Fxlink'%3E%3Cdefs%3E%3Cline%20id%3D'l'%20x1%3D'60'%20x2%3D'60'%20y1%3D'7'%20y2%3D'27'%20stroke%3D'%23fff'%20stroke-width%3D'11'%20stroke-linecap%3D'round'%2F%3E%3C%2Fdefs%3E%3Cg%3E%3Cuse%20xlink%3Ahref%3D'%23l'%20opacity%3D'.27'%2F%3E%3Cuse%20xlink%3Ahref%3D'%23l'%20opacity%3D'.27'%20transform%3D'rotate(30%2060%2C60)'%2F%3E%3Cuse%20xlink%3Ahref%3D'%23l'%20opacity%3D'.27'%20transform%3D'rotate(60%2060%2C60)'%2F%3E%3Cuse%20xlink%3Ahref%3D'%23l'%20opacity%3D'.27'%20transform%3D'rotate(90%2060%2C60)'%2F%3E%3Cuse%20xlink%3Ahref%3D'%23l'%20opacity%3D'.27'%20transform%3D'rotate(120%2060%2C60)'%2F%3E%3Cuse%20xlink%3Ahref%3D'%23l'%20opacity%3D'.27'%20transform%3D'rotate(150%2060%2C60)'%2F%3E%3Cuse%20xlink%3Ahref%3D'%23l'%20opacity%3D'.37'%20transform%3D'rotate(180%2060%2C60)'%2F%3E%3Cuse%20xlink%3Ahref%3D'%23l'%20opacity%3D'.46'%20transform%3D'rotate(210%2060%2C60)'%2F%3E%3Cuse%20xlink%3Ahref%3D'%23l'%20opacity%3D'.56'%20transform%3D'rotate(240%2060%2C60)'%2F%3E%3Cuse%20xlink%3Ahref%3D'%23l'%20opacity%3D'.66'%20transform%3D'rotate(270%2060%2C60)'%2F%3E%3Cuse%20xlink%3Ahref%3D'%23l'%20opacity%3D'.75'%20transform%3D'rotate(300%2060%2C60)'%2F%3E%3Cuse%20xlink%3Ahref%3D'%23l'%20opacity%3D'.85'%20transform%3D'rotate(330%2060%2C60)'%2F%3E%3C%2Fg%3E%3C%2Fsvg%3E");
}

@keyframes swiper-preloader-spin {
  100% {
    transform: rotate(360deg);
  }
}

.swiper-slide > .vide__body,
.swiper-slide > .parallax_cnt {
  height: 100%;
}


// Swiper navigation
//--------------------------------------------------

.swiper-button-prev,
.swiper-button-next {
  position: absolute;
  bottom: 15px;

  display: block;

  font-size: 36px;
  line-height: 1;
  color: $white;

  text-align: center;
  cursor: pointer;
  transition: .2s ease-in;
  will-change: transform;
  z-index: 10;

  &:hover {
    color: $gray-darker;
  }

  @media (min-width: $screen-md-min) {
    bottom: 105px;
  }

  @media (min-width: $screen-lg-min) {
    bottom: 134px;
  }

  // Disabled button
  &.swiper-button-disabled{
    opacity: 0.35;
    cursor: auto;
    pointer-events: none;
  }
}

.swiper-button-prev {
  left: calc(50% - 50px);
  &:before{
    font-family: FontAwesome;
    content: "\f060";
  }

  &:after {
    position: absolute;
    content: '/';
    color: $gray-light;
    right: -20px;
  }
}

.swiper-button-next {
  transform: scale(-1, 1);
  right: calc(50% - 50px);

  &:before{
    font-family: FontAwesome;
    content: "\f061";
  }
}


.swiper-slider.swiper-container-rtl{
  .swiper-button-prev {
    &:before{
      content: "\f061";
    }
  }

  .swiper-button-next {
    &:before{
      content: "\f060";
    }
  }
}



// Swiper pagination
//--------------------------------------------------

.swiper-pagination {
  position: absolute;
  display: block;
  text-align: center;
  transition: .3s;
  transform: translate3d(0, 0, 0);
  z-index: 10;

  &.swiper-pagination-hidden {
    opacity: 0;
  }

  .swiper-pagination-bullet {
    width: 12px;
    height: 12px;
    display: inline-block;
    border-radius: 100%;
    background-color: rgba(255,255,255,.6);
    transition: .2s;

    &.swiper-pagination-bullet-active,
    &:hover{
      background-color: rgba(255,255,255,1);
    }
  }

  &.swiper-pagination-clickable .swiper-pagination-bullet {
    cursor: pointer;
  }
}

// Dark pagination
.swiper-pagination-black{
  .swiper-pagination-bullet {
    background: rgba(0,0,0,.6);

    &.swiper-pagination-bullet-active {
      background: rgba(0,0,0,1);
    }
  }
}


// Swiper pagination orientation
//--------------------------------------------------

// Vertical pagination
.swiper-container-vertical{
  > .swiper-pagination {
    right: 10px;
    top: 50%;
    transform: translate3d(0px, -50%, 0);

    .swiper-pagination-bullet {
      margin: 5px 0;
      display: block;
    }
  }
}


// Horizontal pagination
.swiper-container-horizontal{
  > .swiper-pagination {
    bottom: 20px;
    left: 0;
    width: 100%;

    .swiper-pagination-bullet {
      margin: 0 8px;
    }
  }
}


// Swiper slide styles
//--------------------------------------------------

.swiper-slide {
  position: relative;

  @include display-flex();
  @include align-items(center);
  @include justify-content(center);
  @include flex-shrink(0);
  width: 100%;
  height: 100%;

  white-space: nowrap;

  &-caption {
    width: 100%;
    white-space: normal;

    p {
      display: none;

      @media (min-width: $screen-sm-min) {
        display: block;
      }
    }
  }
}

.swiper__overlay {
  opacity: .90;
  padding: 50px 20px 50px 15px;
  background: $gray-darker;

  @media (min-width: $screen-xs-min) {
    padding-right: 70px;
    padding-left: 20px;
  }

  @media (min-width: $screen-sm-min) {
    @include display-flex();
    min-height: 350px;
    padding-right: 100px;
  }

  @media (min-width: $screen-md-min) {
    min-height: 440px;
  }
  
  @media (min-width: $screen-lg-min) {
    min-height: 465px;
  }
}

.swiper-overlay-center {
  @include align-items(center);
}

.swiper-overlay-end {
  @include align-items(center);

  @media (min-width: $screen-lg-min) {
    padding-bottom: 131px;
    @include align-items(flex-end);
  }
}

.swiper-overlay-item-1 {
  margin-left: auto;
  
  @media (min-width: $screen-lg-min) {
    max-width: 440px;
  }
}

.swiper-overlay-item-2 {
  margin-left: auto;
  margin-right: auto;

  @media (min-width: $screen-lg-min) {
    max-width: 370px;
  }
}

.swiper-overlay-item-3 {
  margin-right: auto;
  margin-left: auto;

  @media (min-width: $screen-lg-min) {
    max-width: 400px;
  }
}

.ie-10,
.ie-11,
.ie-edge {
  .swiper__overlay {
    height: 465px;
  }
}