/*
* Navbar Default
*/

.rd-navbar-default {
  position: relative;
  z-index: 1080;

  .rd-navbar-wrap {
    height: auto !important;
  }

  .rd-navbar {
    position: absolute;
    top: 0;
    right: 0;
    left: 0;
    z-index: 3;
  }

  //=============    Static & Fullwidth     ================
  .rd-navbar-static,
  .rd-navbar-fullwidth {

    .rd-navbar-nav {

      > li {

        > a {

          font-size: 18px;
          min-width: 95px;

          @media (min-width: $screen-lg-min) {
            min-width: 110px;
          }

          @media (min-width: 1400px) {
            min-width: 133px;
          }

          .overlay-skew {
            &:before {
              background: rgba($gray-darker, .9);
            }
          }

          &:after {
            top: 0;
            bottom: 0;
            border-width: 0 1px 0 0;
            border-style: solid;
          }
        }

        & + li {
          > a {
            &:after {
              border-color: rgba($gray-base, .11)
            }
          }
        }

        &:first-child {
          > a {
            &:after {
              border-width: 0 1px 0 1px;
              border-color: $gray-base;
              opacity: .11;
            }
          }
        }
      }
    }

    .rd-navbar-dropdown {
      > li{

        > a {

          &:hover {
            color: $rd-navbar-dropdown-item-hover-color;
          }
        }

        &.focus,
        &.opened {
          > a {
            color: $rd-navbar-dropdown-item-active-color;
          }
        }
      }
    }

    .rd-navbar-megamenu {

      > li {

        // Megamenu column
        > ul {

          // Megamenu link
          a {
            &:hover {
              color: $rd-navbar-megamenu-item-hover-color;
            }
          }
        }
      }
    }

    // RD Navbar Inner
    .rd-navbar-inner {
      max-width: $rd-navbar-width;
      padding-left: 15px;
      padding-right: 15px;
      background: rgba($primary-light, .8);

      @media (min-width: 1400px) {
        padding-left: 35px;
        padding-right: 35px;
      }
    }

    // RD Navbar top panel
    .rd-navbar-top-panel{

      &-inner{
        @include display-flex();
        @include align-items(center);
        @include justify-content(flex-end);
        font-size: 14px;
      }
    }

    .rd-navbar-aside-wrap {
      @include display-flex();
      @include align-items(center);
      @include justify-content(space-between);
      width: 100%;
      max-width: 1460px;
    }

    &.rd-navbar--is-stuck,
    &.rd-navbar--is-clone{
      position: fixed;

      .rd-navbar-inner {
        background: $primary-light;
      }
    }

    .rd-navbar-search {
      .rd-navbar-search-toggle {
        color: $primary;

        &:hover {

          span,
          .search-content {
            color: $white;
          }
        }

        span {
          top: 1px;
          margin-left: 10px;
        }
      }

      .rd-search-form-submit{

        &:hover {
          color: $primary;
        }
      }
    }

    .forms-modal {

      > * {
        position: relative;
        width: auto;
        min-width: 96px;
        padding: 12px 10px 13px;
        z-index: 1;
      }

      a {
        color: $primary;

        &:before {
          position: absolute;
          content: '';
          top: 0;
          left: 0;
          right: 0;
          bottom: 100%;
          transform: skew(-7deg);
          will-change: transform;
          outline: 1px solid transparent;
          -webkit-backface-visibility: hidden;
          animation: out .3s;
          z-index: -1;
          background: #a5b938;
        }

        &:hover {
          color: $gray-darker;

          &:before {
            animation: in .3s forwards;
          }
        }
      }
    }

    * + .list-inline {
      margin-left: 20px;
    }

    * + .rd-navbar-collapse {
      margin-left: 22px;
    }
    
    @media (min-width: 1920px) {
      .rd-navbar-aside-wrap {
        max-width: 100%;

        &:after {
          content: '';
          width: 212px;
        }
      }  
    }
  }

  .rd-navbar-static {

    .logo-long {
      display: block;
    }

    .logo-short {
      display: none;
    }

    .rd-navbar-inner {
      @include justify-content(flex-start);
    }
  }

  .rd-navbar-fullwidth {

    .logo-long {
      display: none;
    }

    .logo-short {
      display: block;
    }

    .rd-navbar-aside-right {
      margin-top: 0;
    }
  }

  .rd-megamenu-header {
    color: $primary;
  }

  .modal-custom {

    .close {

      span {
        color: $primary;
      }

      &:hover {
        span {
          color: $gray-darker;
        }
      }
    }
  }

  .forms-modal {

    > * {
      width: 100%;
    }
  }
}

.rd-navbar-variant-3,
.rd-navbar-default {
  // Fixed Layout

  .rd-navbar-fixed {

    // RD Navbar nav
    .rd-navbar-nav {

      li {

        &:hover,
        &.active,
        &.opened{
          > a{
            color: $rd-navbar-fixed-nav-active-color;
            background: $rd-navbar-fixed-nav-active-background;
          }
          > .rd-navbar-submenu-toggle {
            color: $rd-navbar-fixed-nav-active-color;
          }
        }
      }
    }

    .rd-navbar-panel {

      &:before {
        background: $rd-navbar-fixed-nav-background-1;
      }
    }

    .rd-navbar-search{
      .rd-search-form-submit{

        &:hover {
          color: $primary;
        }
      }
    }

    .logo-long { display: none; }

    .forms-modal {
      a {
        color: $gray-darker;

        &:hover {
          color: $primary;
        }
      }
    }
  }

  // Scrollbar styles
  .rd-navbar-fixed .rd-navbar-nav-wrap,
  .rd-navbar-sidebar .rd-navbar-sidebar-inner  {
    &::-webkit-scrollbar-thumb {
      background: lighten($primary, 20%);
    }

    &::-webkit-scrollbar-track {
      background: $primary;
    }
  }
}

.rd-navbar-variant-3 {

  position: relative;
  z-index: 1080;

  .rd-navbar-sidebar {

    .rd-navbar-inner {
      display: inline-block;
      max-width: 218px;
    }
  }

  .overlay-skew {
    display: none;
  }

  .rd-navbar-fixed {

    .rd-megamenu-header {
      color: $primary;
    }

    .forms-modal {

      > * {
        width: 100%;
      }
    }

    .logo-dark { display: none; }
  }

  .modal-custom {

    .close {

      span {
        color: $primary;
      }

      &:hover {
        span {
          color: $gray-darker;
        }
      }
    }
  }

  .modal-dialog {
    margin-right: auto;
  }
}