//
// Form styles
// --------------------------------------------------

.rd-mailform {
  position: relative;
  text-align: center;
  
  .form-wrap {
    text-align: left;
  }

  * + .button-wrap,
  * + .button {
    margin-top: 14px;
  }
  
  @media (min-width: $screen-sm-min) {
    text-align: left;
  }
}

input:-webkit-autofill ~ label,
input:-webkit-autofill ~ .form-validation {
  color: $gray-base !important;
}

.form-label,
.form-label-outside {
  margin-bottom: 0;
  font-weight: 400;
  color: $input-color-placeholder;
}

.form-label-dark {
  color: $gray-darker;
}

.form-variant-1 {

  .form-input {
    padding-top: 12px;
    padding-bottom: 12px;
    background: $white;
    border: 1px solid $gray-lighter;
  }
}

.form-input {
  display: block;
  width: 100%;
  min-height: $input-height;
  padding: $input-padding-vertical $input-padding-horizontal;
  font-size: $input-font-size;
  font-weight: $input-font-weight;
  line-height: $input-line-height;
  color: $input-color;
  letter-spacing: .05em;
  background-color: $input-bg;
  background-image: none;
  border-radius: $input-border-radius;
  -webkit-appearance: none;
  transition: .3s ease-in-out;

  @if ($input-border-obj) {
    border: $input-border-obj;
  }

  &:focus {
    outline: 0;
  }

  &::-webkit-input-placeholder { /* Chrome/Opera/Safari */
    color: $gray-darker;
  }
  &::-moz-placeholder { /* Firefox 19+ */
    color: $gray-darker;
  }
  &:-ms-input-placeholder { /* IE 10+ */
    color: $gray-darker;
  }
  &:-moz-placeholder { /* Firefox 18- */
    color: $gray-darker;
  }
}

.form-coupon {

  .form-input {
    padding-top: 8px;
    padding-bottom: 12px;
    line-height: 25px;
    max-width: 153px;
  }

  .form-label {
    top: 22px;
    left: 50%;
    width: 100%;
    text-align: center;
    transform: translate(-50%, -50%);
  }
}

.ie-10,
.ie-11,
.ie-edge {
  .form-coupon {

    .form-input {
      padding-top: 11px;
      padding-bottom: 9px;
      min-height: 47px;
    }
  }

  .form-label {
    top: 26px;
  }
}

.rd-mailform-lg {

  .form-input {
    min-height: 60px;
    padding-top: 18px;
    padding-bottom: 18px;
  }

  .form-label {
    top: 29px;
  }
}

.form-inline {
  .form-input {
    padding-left: 16px;
    padding-right: 16px;
  }

  .form-label {
    left: 16px;
  }
}

textarea.form-input {
  height: $textarea-default-height;
  min-height: $textarea-default-min-height;
  max-height: $textarea-default-max-height;
  resize: vertical;
}

// Form wrapper
// -------------------------

.form-wrap {
  position: relative;

  .button {
    width: 100%;
  }

  .recaptcha {
    > div {

      margin-left: auto;
      margin-right: auto;

      @media (min-width: $screen-sm-min) {
        margin-left: 0;
      }
    }
  }
}

.form-wrap + .form-wrap {
  margin-top: 15px;
}

// Form labels
// -------------------------

.form-label {
  position: absolute;
  top: $input-height / 2 - 1px;
  left: $input-padding-horizontal;
  font-size: $input-font-size;
  font-weight: $input-font-weight;
  line-height: $input-line-height;
  letter-spacing: .05em;
  pointer-events: none;
  z-index: 9;
  transition: .25s;
  will-change: transform;
  transform: translateY(-50%);

  &.focus {
    opacity: 0;
  }

  &.auto-fill {
    color: $input-color;
  }
}

.form-label-outside {
  margin-bottom: 10px;
  font-size: $font-size-base;
  letter-spacing: 0;
  @media (min-width: $screen-sm-min) {
    position: static;

    &,
    &.focus,
    &.auto-fill {
      transform: none;
    }
  }
}

.form-label-outside ~ .form-validation {
  top: 31px;
}

// Form validation
// -------------------------

.form-validation {
  position: absolute;
  right: 4px;
  top: 1px;
  z-index: 11;
  margin-top: 2px;
  font-size: 10px;
  font-weight: 400;
  line-height: 12px;
  letter-spacing: 0;
  color: $error-color;
  transition: .3s;
}

.form-wrap-captcha {
  max-width: 304px;
  margin-left: auto;
  margin-right: auto;

  .form-validation {
    top: -2px;
  }

  @media (min-width: $screen-sm-min) {
    margin-left: 0;
  }
}

.form-validation-left {
  .form-validation {
    right: auto;
    top: 100%;
    left: 0;
  }
}


// Form output
// -------------------------

#form-output-global {
  position: fixed;
  bottom: 30px;
  left: 15px;
  visibility: hidden;
  transform: translateX(-500px);
  transition: .3s all ease;
  z-index: 9999999;

  &.active {
    transform: translateX(0);
    visibility: visible;
  }

  @media (min-width: $screen-xs) {
    left: 30px;
  }
}

.form-output {
  position: absolute;
  top: 100%;
  left: 0;
  font-size: 14px;
  line-height: 1.5;
  margin-top: 2px;
  transition: .3s;
  opacity: 0;
  visibility: hidden;

  &.active {
    opacity: 1;
    visibility: visible;
  }

  &.error {
    color: $error-color;
  }

  &.success {
    color: $success-color;
  }
}

// Radio and Checkbox Custom
// -------------------------

// Base Styles
//
.radio .radio-custom,
.radio-inline .radio-custom,
.checkbox .checkbox-custom,
.checkbox-inline .checkbox-custom {
  opacity: 0;

  &, &-dummy {
    position: absolute;
    width: 15px;
    height: 15px;
    margin-left: -30px;
    margin-top: 3px;
    outline: none;
    cursor: pointer;
    border: 1px solid $gray-2;
  }

  &-dummy {
    pointer-events: none;
    background: transparent;

    &:after {
      position: absolute;
      opacity: 0;
    }
  }

  &:focus {
    outline: none;
  }
}

.radio-custom:checked + .radio-custom-dummy:after,
.checkbox-custom:checked + .checkbox-custom-dummy:after {
  opacity: 1;
}

// Custom Radio Styles
//
.radio,
.radio-inline {
  .radio-custom-dummy {

    &:after {
      content: '\e5ca';
      font-family: 'Material Icons';
      font-size: 20px;
      line-height: 10px;
      position: absolute;
      top: 0;
      left: 1px;
      color: $gray-darker;
    }
  }
}

// Custom Checkbox Styles
//
.checkbox,
.checkbox-inline {
  padding-left: 26px;

  .checkbox-custom-dummy {
    pointer-events: none;
    border-radius: 0;
    margin-left: 0;
    left: 0;

    &:after {
      content: '\e5ca';
      font-family: 'Material Icons';
      font-size: 20px;
      line-height: 10px;
      position: absolute;
      top: 0;
      left: 1px;
      color: $gray-darker;
    }
  }

  label {
    padding-left: 0;
    font-weight: 700;
    transition: .3s;
    
    &:hover {
      color: $primary;
    }
  }
}

.radio-inline {
  padding-left: 30px;
}

.checkbox input[type="checkbox"] {
  margin-left: -26px;
}

// Custom
.form-grid {
  text-align: left;
}

@media (max-width: $screen-xs-max) {
  .form-grid {
    max-width: 370px;
    margin-left: auto;
    margin-right: auto;
  }
}

* + .form-grid {
  margin-top: 30px;
}

.page .form-inline {

  > * + * {
    margin-top: 20px;
  }

  @media (min-width: $screen-sm-min) {
    @include display-flex;
    @include flex-direction(row);
    @include flex-wrap(nowrap);
    @include align-items(stretch);
    @include justify-content(center);
    width: 100%;

    &-custom {
      @include align-items(flex-end);

      .button {
        max-height: 42px;
      }
    }

    > * + * {
      margin-top: 0;
      margin-left: 10px;
    }

    .form-wrap {
      @include flex-grow(1);
    }

    .form-input {
      width: 100%;
    }

    button {
      display: inline-block;
      @include flex-shrink(0);
      line-height: 24px;
      padding-top: 7px;
      padding-bottom: 11px;
    }
  }
}

.form-label-centered {
  .form-label-outside {
    display: block;
    text-align: center;
  }

  @media (min-width: $screen-sm-min) {
    * + .button {
      margin-top: 45px;
    }
  }
}

.page {

  .rd-mailform.form-white-outline {

    .form-label {
      top: $input-height / 2 + 2px;
    }
  }

  .form-gray-outline {

    .form-input {
      background: transparent;
      border-color: rgba($white, .1);
    }

    .form-validation {
      color: $white;
    }
  }
}

[class*="form-button-within"] {
  .form-wrap {
    position: relative;
  }

  * + .button {
    margin-top: 0;
  }
}

.form-button-within-1 {

  .form-input {
    padding-right: 100px;
  }

  .button {
    position: absolute;
    width: auto;
    min-width: auto;
    top: 50%;
    right: 20px;
    transform: translateY(-55%);
    padding: 0;
  }

  .form-validation {
    left: 20px;
  }
}

.form-button-within-2 {

  .form-input {
     padding-top: 12px;
     padding-right: 120px;
     padding-bottom: 12px;
     min-height: 50px;
  }

  .button {
     position: absolute;
     top: 50%;
     right: -5px;
     width: auto;
     min-height: 50px;
     transform: translateY(-50%);
  }

  .form-validation {
     right: 115px;
  }

  @media (max-width: $screen-xxs-max) {

    .button {
      min-width: 100px;
      padding-left: 20px;
      font-size: 14px;
    }
  }

  @media (min-width: $screen-xs-min) {
    .form-validation {
      right: 162px;
    }

    .form-input {
      padding-right: 170px;
    }
  }
}

.form-button-within-3 {

  .form-input {
    padding-right: 120px;
  }

  .button {
    position: absolute;
    top: 50%;
    right: -5px;
    width: auto;
    min-height: 42px;
    transform: translateY(-50%);
  }

  .form-validation {
    right: 115px;
  }

  @media (max-width: $screen-xxs-max) {

    .button {
      min-width: 100px;
      padding-left: 20px;
      font-size: 14px;
    }
  }

  @media (min-width: $screen-xs-min) {
    .form-validation {
      right: 162px;
    }

    .form-input {
      padding-right: 170px;
    }
  }
}

.checkbox-inline {
  input[type="checkbox"] {
    margin-left: -25px;
  }
}

.text-label {
  margin-bottom: 10px;
}

.forms-modal {

  > * {
    display: inline-block;
  }

  a + a {
    margin-top: 5px;
  }

  @media (min-width: $screen-md-min) {
    a + a {
      margin-top: 0;
    }
  }
}

.form-search {

  button.icon[type="submit"] {
    padding: 0 2px;
    font-size: 24px;
    position: absolute;
    top: 50%;
    right: 8px;
    transform: translateY(-40%);
    border: none;
    background: transparent;
    transition: color 300ms;

    &:hover {
      color: $gray-darker;
    }
  }
}

button[type="submit"] {
  &:focus {
    outline: none;
  }
}

.form-control {
  box-shadow: none;
}

.form-control:focus {
  outline: none;
  box-shadow: none;
  border-color: $gray-2;
}

.form-search-input {
  padding-right: 45px;
}