/*
* Sidebar Layout
*/

.rd-navbar-sidebar {
  display: block;

  .rd-navbar-sidebar-toggle {
    position: fixed;
    top: 8px;
    left: 8px;
    display: block;
    z-index: 999; // More than .rd-navbar-sidebar-inner
    background: rgba($gray-base, 0.6);
    @extend %rd-navbar-transition;
    transition-duration: 600ms;
  }

  .forms-modal {
    > a {
      display: inline-block;
      width: 100%;

      & + & {
        margin-top: 10px;
      }
    }
  }

  .rd-navbar-sidebar-toggle {

    &.active {
      transition-duration: 200ms;
      background: transparent;
      
      span,
      span:after,
      span:before {
        background: $primary;
      }
    }
  }

  @media (min-width: $screen-md-min) {

    .forms-modal {
      font-size: 14px;
      line-height: 24px;

      > a {
        width: auto;
        color: $primary-sec;

        &:hover {
          color: $gray-darker;
        }
      }

      > a + a {
        margin-left: 20px;
      }

      > a + a {
        position: relative;
        width: auto;

        &:before {
          position: absolute;
          content: '/';
          top: 50%;
          left: -14px;
          color: $primary-sec;
          transform: translateY(-50%);
        }
      }
    }
  }

  @media (min-width: 1600px) {
    .rd-navbar-sidebar-toggle{
      display: none;
    }
  }

  .rd-megamenu-header {
    font-size: 18px;
    margin-top: 20px;
    color: $gray-darker;
  }

  * + .rd-megamenu-list { margin-top: 15px; }
  * + .forms-modal { margin-top: 8px; }

  .logo-white { display: none; }
  .logo-dark { display: block; }
}


.rd-navbar-sidebar .rd-navbar-sidebar-inner {
  position: fixed;
  display: flex;
  flex-direction: column;
  padding: 55px 38px 50px 45px;
  box-shadow: none;
  @extend %rd-navbar-transition;
  transform: translateX(-100%);

  &:before,
  &:after {
    position: absolute;
    top: 0;
    right: 0;
    content: ' ';
    will-change: trasform;
  }

  &:before {
    height: 150px;
    width: 130px;
    background: $gray-base;
    transform: skewX(43deg);
    transform-origin: 0 0;
  }

  &:after {
    height: 175px;
    width: 52px;
    background: $primary;
    transform: skewX(17deg);
    transform-origin: 0 0;
    z-index: 1;
  }

  &.active {
    transform: translateX(0);
  }

  @media (min-width: 1600px) {
    transform: translateX(0);
  }

  .rd-navbar-top-panel{
    order: 1;
    padding: 0 20px;

    text-align: left;
  }

  .rd-navbar-brand{
    margin-bottom: 30px;
  }

  // Base search style
  .rd-navbar-search {
    display: inline-flex;
    margin-top: 30px;
    padding: 0;

    .rd-search{
      position: relative;
    }

    .form-input{
      padding-right: 50px;
    }

    .rd-search-form-submit{
      position: absolute;
      top: 0;
      bottom: 0;
      right: 0;

      font-size: 18px;
      line-height: 24px;
      width: 50px;
      padding: 0;
      border: none;

      background-color: transparent;
      color: $primary;

      &:hover {
        color: $primary;
      }
    }

    .rd-search-results-live{
      display: none;
    }

    .form-label {
      color: $primary;
    }
  }


  // RD Navbar nav
  .rd-navbar-nav {
    display: block;
    font-size: 18px;
    line-height: 24px;
    text-align: left;

    &:before, &:after {
      content: '';
      display: block;
      height: 24px;
    }

    li {
      > a {
        display: block;
        padding: 4px 56px 7px 0;
        color: $primary;
        border-bottom: 1px solid $gray-2;

        &:first-letter {
          text-transform: uppercase;
        }
      }

      &:hover,
      &.active,
      &.opened{
        > a{
          color: $gray-darker;
          background: transparent;
        }
        > .rd-navbar-submenu-toggle {
          color: $gray-darker;
        }
      }

      &.opened {
        > .rd-navbar-submenu-toggle {
          &:after {
            transform : rotate(180deg);
          }
        }
      }

      & + li{
        margin-top: 4px;
      }
    }

    .rd-navbar-submenu-toggle {
      cursor: pointer;
      color: $primary;

      &::after {
        content: '\f236';
        position: absolute;
        top: 22px;
        right: 0;
        margin-top: -22px;
        width: 65px;
        height: 36px;
        font: 400 20px "Material Design Icons";
        line-height: 36px;
        text-align: center;
        transition: 0.3s transform ease;
        z-index: 2;
      }
    }
  }


  // RD navbar submenu styles
  .rd-navbar-dropdown,
  .rd-navbar-megamenu {
    display: none;
    margin-top: 4px;
  }

  .rd-navbar-submenu {
    position: relative;
    @extend %rd-navbar-transition;

    .rd-navbar-megamenu ul > li > a,
    .rd-navbar-dropdown > li > a {
      padding-left: 15px;
      padding-right: 15px;
    }

    .rd-navbar-dropdown li li > a,
    .rd-navbar-megamenu ul li li > a {
      padding-left: 48px;
    }

    &.opened {
      > .rd-navbar-dropdown,
      > .rd-navbar-megamenu {
        display: block;
      }
    }
  }

  &::-webkit-scrollbar-thumb {
    background: $primary;
  }

  &::-webkit-scrollbar-track {
    background: rgba($primary, .3);
  }
}