//
// Utilities custom
// --------------------------------------------------

.block-centered {
  margin-left: auto;
  margin-right: auto;
}

.offset-left-sm-0 {
  
  @media (min-width: $screen-sm-min) {
    margin-left: 0;
  }
}

.text-width-280 {
  max-width: 280px;
}

.text-width-350 {
  max-width: 350px;
}

.text-width-370 {
  max-width: 370px;
}

.text-width-smaller {
  max-width: 400px;
}

.text-width-small {
  max-width: 450px;
}

.text-width-500 {
  max-width: 500px;
}

.text-width-medium {
  max-width: 550px;
}

.text-width-600 {
  max-width: 600px;
}

.text-width-650 {
  max-width: 650px;
}

.text-width-large {
  max-width: 700px;
}


@media (min-width: $screen-md-min) {
  .text-width-md-340 {
    max-width: 340px;
  }

  .text-width-md-370 {
    max-width: 370px;
  }

  .text-width-md-440 {
    max-width: 440px;
  }
}

@media (min-width: $screen-lg-min) {
  .max-width-lg-94 {
    max-width: 94%;
  }

  .width-lg-102 {
    width: 102%;
  }
}

.text-center > [class*='text-width'] {
  margin-left: auto;
  margin-right: auto;
}

// inset custom

@media (min-width: $screen-xs-min) {
  .inset-xs-left-15 {
    padding-left: 15px;
  }
}

@media (min-width: $screen-sm-min) {
  .inset-sm-left-100 {
    padding-left: 100px;
  }
}

@media (min-width: $screen-md-min) {

  .inset-md-left-85 {
    padding-left: 85px;
  }
}

@media (min-width: $screen-lg-min) {

  .inset-lg-right-100 {
    padding-right: 100px;
  }
}

// offset

html {

  @media (max-width: $screen-xs-max) {
    .offset-sm-top-30 + .offset-sm-top-30 {
      margin-top: 30px !important;
    }
  }

  @media (min-width: $screen-md-min) {
    .offset-md-top-15 {
      margin-top: 15px;
    }
  }
}


// letter-spacing

.spacing-20 {
  letter-spacing: .02em;
}

.spacing-75 {
  letter-spacing: .075em;
}

// Order
//

.order-xs-last {

  @media (max-width: $screen-xs-max) {
    order: 1;
  }
}

.order-md-last {

  @media (max-width: $screen-md-max) {
    order: 1;
  }
}

// Images
.img-cover {
  img {
    width: 100%;
  }
}

// Context Custom