/*
*
* Lists
* --------------------------------------------------
*/


ul,
ol {
  list-style: none;
  padding: 0;
  margin: 0;
}


.list {

  > li + li {
    margin-top: 11px;
  }
}


// List inline
//

.list-inline {
  margin-left: -5px;
  margin-right: -5px;

  > li {
    padding-left: 6px;
    padding-right: 6px;
  }
}

.list-inline-2 {
  margin-left: -8px;
  margin-right: -8px;

  > li {
    padding-left: 8px;
    padding-right: 8px;
  }
}

.list-tags {
  margin-left: -6px;
  margin-right: -6px;
  transform: translateY(-6px);
  margin-bottom: -6px;

  > li {
    position: relative;
    display: inline-block;
    padding-left: 6px;
    padding-right: 6px;
    margin-top: 6px;
    
    &:before {
      position: absolute;
      content: '';
      top: 0;
      right: -2px;
      bottom: 0;
      width: 1px;
      background: $primary;
    }
    
    &:last-child {
      &:before {
        display: none;
      }
    }
  }

  a {
    font-weight: 700;
  }
}


// Description Lists
//

dl {
  margin: 0;
}


// List terms
//

.list-terms {
  margin-top: 35px;
  dt + dd { margin-top: 5px; }
  dd + dt { margin-top: 25px; }
}


// List index
//

.list-index {
  counter-reset: li;

  li {
    .list-index-counter {
      &:before {
        content: counter(li, decimal)". ";
        counter-increment: li;
      }
    }
  }
}


// List marked
//

.list-marked {
  text-align: left;

  > li {
    position: relative;
    padding-left: 12px;

    &:before {
      position: absolute;
      top: -1px;
      left: 0;
      content: '-';
    }
  }

  > li + li {
    margin-top: 11px;
  }

  .list-marked {
    margin-top: 8px;

    > li + li {
      margin-top: 10px;
    }
  }
}

.list-marked-2 {
  text-align: left;

  > li {
    position: relative;
    padding-left: 12px;

    &:before {
      position: absolute;
      top: -1px;
      left: 0;
      content: '+';
    }
  }

  > li + li {
    margin-top: 11px;
  }
}

.list-comments {
  text-align: left;

  > li {
    position: relative;
    padding-left: 26px;

    &:before {
      position: absolute;
      top: -1px;
      left: 0;
      content: '\f277';
      font-size: 18px;
      font-weight: 400;
      font-family: "Material Design Icons";
    }
  }

  > li + li {
    margin-top: 11px;
  }
}

.list-marked-none {
  text-align: left;

  li + li { margin-top: 10px; }
}

// Ordered list
.list-ordered {
  text-align: left;
  counter-reset: li;

  > li {
    position: relative;
    padding-left: 21px;

    &:before {
      position: absolute;
      display: inline-block;
      top: 0;
      left: 0;
      width: 15px;
      font-weight: 400;
      color: $gray-light;
      content: counter(li, decimal-leading-zero) '.';
      counter-increment: li;
    }
  }

  > li + li {
    margin-top: 11px;
  }

  .start-from-4:before {
    counter-increment: li +4;
  }

  .start-from-5:before {
    counter-increment: li +5;
  }

  .start-from-6:before {
    counter-increment: li +6;
  }
}

// Two columns list
.list-two-columns {
  > * {
    display: inline-block;
    width: 45%;
  }
}

.list-md-two-columns {
  @media (min-width: $screen-md-min) {
    > * {
      display: inline-block;
      width: 45%;
    }
  }
}

// Custom styles list

.list-size-big {

  &.list-ordered {
    > li {
      padding-left: 28px;
    }

    > li + li { margin-top: 2px; }
  }

  > li {
    font-size: 18px;
    line-height: 27px;

    &:before {
      font-size: 18px;
    }
  }

  > li {
    padding-left: 15px;
  }
}

.list-size-normal {
  > li {
    font-size: 14px;
    line-height: 24px;

    &:before {
      font-size: 14px;
    }
  }
}

.list-family-base {
  font-family: $font-family-base;

  a {
    font-family: inherit;
  }
}

.list-bold {
  font-weight: 700;

  li:before { font-weight: inherit; }
}

.list-uppercase {
  text-transform: uppercase;
}

.list-white {
  color: $white;
}

.list-primary-sec {
  color: $primary-sec;

  a {
    @include link($primary-sec, $gray-darker);
  }
}

.list-primary-sec-2 {
  color: $primary-sec;

  a {
    @include link($primary-sec, $gray-light);
  }

  li.active {
    > a {
      color: $gray-light;
    }
  }
}

.list-gray-light {
  color: $gray-light;

  a {
    @include link(inherit, $primary);
  }
}

.list-gray-darker {
  color: $gray-darker;
}

.list-xs-inline-block {

  @media (max-width: $screen-xs-max) {
    display: inline-block;
  }
}



// Custom styles list
.icon-list-var-1 {

  a {
    @include link($primary-sec, $gray-darker);
  }
}

.icon-list-var-2 {

  a {
    @include link($white, $primary);
  }

  h5 + p { margin-top: 14px; }

  @media (min-width: $screen-lg-min) {
    .box-icon-content {
      max-width: 80%;
      margin-left: auto;
      margin-right: auto;
    }
  }
}

.list-site-map {
  text-align: left;
  padding-left: 15px;

  li a {

    &:before {
      content: '-';
      margin-right: 6px;
      margin-left: -12px;
      opacity: 0;
      visibility: hidden;
      transition: inherit;
    }

    &:hover {

      &:before {
        margin-left: 0;
        opacity: 1;
        visibility: visible;
        color: $gray-darker;
      }
    }
  }

  li + li { margin-top: 10px; }
}

.list-heading {
  a {
    @include link($gray-darker, $primary);
  }
}

* + .list-heading { margin-top: 30px; }
* + .list-site-map { margin-top: 14px; }
.list-heading + .list-heading { margin-top: 30px; }