/*
*
* Page Loaders
* --------------------------------------------------
*/

$color-1:               $gray-base;

// Base styles
// -------------------------

.page-loader {
  position: fixed;
  left: 0;
  top: 0;
  bottom: 0;
  right: 0;
  float: left;
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 20px;
  z-index: 9999999;
  background: #fff;
  transition: .3s all ease;

  &.loaded {
    opacity: 0;
    visibility: hidden;
    z-index: -1;
  }

  &.ending {
    display: none;
  }

  .page-loader-body {
    text-align: center;
  }

  h1 {
    margin-top: 200px;
    font-style: italic;
    font-family: 'Kanit', sans-serif;
  }
}

// Custom Styles
// -------------------------

html {

  .centerBike {
    position: absolute;
    top: 50%;
    left: 50%;
    margin-top: 26px;
    margin-left: -12px;
  }

  .center {
    position: absolute;
    top: 50%;
    left: 50%;
    margin-top: -50px;
    margin-left: -50px;
  }
}

#loop {
  height: 108px;
  width: 108px;
  border: $color-1 solid 4px;
  border-radius: 200px;
}

#loop:before {
  background: -moz-linear-gradient(left, rgba($color-1, 0) 0%, rgba($color-1, 1) 30%, rgba($color-1, 1) 70%, rgba($color-1, 0) 100%);
  background: -webkit-gradient(linear, left top, right top, color-stop(0%, rgba($color-1, 0)), color-stop(30%, rgba($color-1, 1)), color-stop(70%, rgba($color-1, 1)), color-stop(100%, rgba($color-1, 0)));
  background: -webkit-linear-gradient(left, rgba($color-1, 0) 0%, rgba($color-1, 1) 30%, rgba($color-1, 1) 70%, rgba($color-1, 0) 100%);
  background: -o-linear-gradient(left, rgba($color-1, 0) 0%, rgba($color-1, 1) 30%, rgba($color-1, 1) 70%, rgba($color-1, 0) 100%);
  background: -ms-linear-gradient(left, rgba($color-1, 0) 0%, rgba($color-1, 1) 30%, rgba($color-1, 1) 70%, rgba($color-1, 0) 100%);
  background: linear-gradient(to right, rgba($color-1, 0) 0%, rgba($color-1, 1) 30%, rgba($color-1, 1) 70%, rgba($color-1, 0) 100%);

  content: "";
  display: block;
  height: 4px;
  will-change: transform;
  transform: translateX(-50%);
  position: relative;
  top: 100px;
  left: 50%;
  width: 380px;
}

#bike-wrapper {
  height: 108px;
  width: 108px;
  will-change: transform;
  animation: drive 3s linear infinite;
}

#bike {
  height: 24px;
  width: 25px;
  background-image: url("../images/motorbike.png");
}

@keyframes drive {
  0% {
    margin-left: -364px;
    opacity: 0;
  }
  33.33% {
    transform: rotate(0deg);
    margin-left: -50px;
    opacity: 1;
  }
  66.66% {
    transform: rotate(-360deg);
    margin-left: -50px;
    opacity: 1;
  }
  100% {
    margin-left: 264px;
    transform: rotate(-360deg);
    opacity: 0;
  }
}