/*
*
* RD Navbar
* --------------------------------------------------
*/

//== RD Navbar variables
//
//##

$rd-navbar-min-font-size: 24px;
$rd-navbar-min-line-height: 48px;
$rd-navbar-min-height: 56px;

$rd-navbar-color: $white;
$rd-navbar-background: transparent;
$rd-navbar-shadow: 0 0 22px -4px rgba(0, 0, 0, 0.17);
$rd-navbar-width: 100%;

//RD Navbar panel
$rd-navbar-panel-color: $rd-navbar-color;
$rd-navbar-panel-background: $rd-navbar-background;

//RD Navbar nav
$rd-navbar-nav-min-width: 270px;
$rd-navbar-nav-color: $rd-navbar-color;
$rd-navbar-nav-background: transparent;
$rd-navbar-nav-hover-color: $primary;
$rd-navbar-nav-hover-background: transparent;
$rd-navbar-nav-active-color: $rd-navbar-nav-hover-color;
$rd-navbar-nav-active-background: $rd-navbar-nav-hover-background;

//RD Navbar dropdown
$rd-navbar-dropdown-width: 295px;
$rd-navbar-dropdown-background: $gray-darker;
$rd-navbar-dropdown-item-color: $white;
$rd-navbar-dropdown-item-background: transparent;
$rd-navbar-dropdown-item-hover-color: $primary;
$rd-navbar-dropdown-item-hover-color-2: $primary-sec;
$rd-navbar-dropdown-item-hover-background: transparent;
$rd-navbar-dropdown-item-active-color: $rd-navbar-dropdown-item-hover-color;
$rd-navbar-dropdown-item-active-color-2: $rd-navbar-dropdown-item-hover-color-2;
$rd-navbar-dropdown-item-active-background: $rd-navbar-dropdown-item-hover-background;

//RD Navbar megamenu
$rd-navbar-megamenu-gutter: 30px;
$rd-navbar-megamenu-background: $gray-darker;
$rd-navbar-megamenu-item-color: $white;
$rd-navbar-megamenu-item-background: transparent;
$rd-navbar-megamenu-item-hover-color: $primary;
$rd-navbar-megamenu-item-hover-color-2: $primary-sec;
$rd-navbar-megamenu-item-hover-background: transparent;

//RD Navbar togles
$rd-navbar-toggle-preset: "button-lines-arrow-2";
$rd-navbar-collapse-toggle-preset: "collapse-preset-1";

//RD Navbar Fixed
$rd-navbar-fixed-height: $rd-navbar-min-height;
$rd-navbar-fixed-line-height: $rd-navbar-min-line-height;
$rd-navbar-fixed-toggle-color: $white;

$rd-navbar-fixed-panel-color:  $gray-darker;
$rd-navbar-fixed-panel-background: $white;

$rd-navbar-fixed-nav-color: $gray-darker;
$rd-navbar-fixed-nav-background-1: $primary;
$rd-navbar-fixed-nav-background-2: $primary-sec;

$rd-navbar-fixed-nav-active-color: $white;
$rd-navbar-fixed-nav-active-background: $primary;
$rd-navbar-fixed-nav-active-background-2: $primary-sec;

$rd-navbar-fixed-shadow: 0 0 12px 1px rgba(0, 0, 0, 0.25);

//RD Navbar indents
$navbar-static-nav-indent: -4px;
$navbar-fullwidth-nav-indent: -4px;


@import "rd-navbar_includes/_rd-navbar-mixins";


// RD Navbar Basics
%rd-navbar-transition {
  transition: 0.35s all cubic-bezier(0.65, 0.05, 0.36, 1);
}

%rd-navbar-hidden {
  display: none;
}

%rd-navbar-visible {
  display: block;
}

.rd-navbar--no-transition {
  &, * {
    transition: none !important;
  }
}

.rd-navbar-wrap {
  @extend %rd-navbar-transition;
}

.rd-navbar, .rd-navbar.rd-navbar--is-clone {
  @extend %rd-navbar-hidden;
}

.rd-navbar-fixed,
.rd-navbar-static,
.rd-navbar-fullwidth,
.rd-navbar-sidebar {
  @extend %rd-navbar-visible;
}

.rd-navbar,
.rd-navbar-dropdown,
.rd-navbar-megamenu,
.rd-navbar-nav,
.rd-navbar-panel{
  @extend %rd-navbar-transition;
}


// RD Navbar Collapse
.rd-navbar-collapse {
  &-toggle {
    @include make-toggle(
            $rd-navbar-collapse-toggle-preset,
            $rd-navbar-min-line-height,
            $rd-navbar-min-font-size,
            $rd-navbar-panel-color
    );
    display: none;
  }
}

.rd-navbar--is-stuck {
  box-shadow: $rd-navbar-shadow;
}

.rd-navbar.rd-navbar-fixed + .rd-navbar.rd-navbar--is-clone,
.rd-navbar.rd-navbar-sidebar + .rd-navbar.rd-navbar--is-clone {
  display: none;
}

@import "rd-navbar_includes/_rd-navbar-components";


/*
* @subsection   Hybrid  Styles
*/

//=============    Static & Fullwidth     ================
.rd-navbar-static,
.rd-navbar-fullwidth {

  // RD Navbar Nav
  .rd-navbar-nav {
    text-align: center;

    > li {
      display: inline-block;
      transition: .25s;

      > a {
        position: relative;
        display: inline-block;
        min-width: 95px;
        padding: 26px 10px;
        color: $rd-navbar-nav-color;
        line-height: 1.33;
        transition: .25s;
        z-index: 1;

        .overlay-skew {
          position: absolute;
          content: '';
          top: 0;
          right: 0;
          bottom: 0;
          left: 0;
          z-index: -1;

          transform: skew(-7deg) scale(.9999);
          text-rendering: optimizeLegibility;
          will-change: transform;
          outline: 1px solid transparent;
          -webkit-backface-visibility: hidden;

          &:before {
            position: absolute;
            content: '';
            left: 0;
            right: 0;
          }

          &:before {
            top: 0;
            bottom: 100%;
            animation: out .3s;
            z-index: -1;
          }

          &:after {
            z-index: 1;
          }
        }

        &:after {
          position: absolute;
          content: '';
          left: 0;
          right: 0;
          transform: skew(-7deg);
          will-change: transform;
        }

        &:hover{

          .overlay-skew {

            &:before {
              animation: in .3s forwards;
            }
          }
        }
      }

      > .rd-navbar-submenu-toggle{
        margin-left: 4px;
        font-family: "Material Design Icons";
        font-size: 16px;
        cursor: pointer;
        display: none;

        &:hover{
          color: $rd-navbar-nav-hover-color;
        }

        &:before{
          content: '\f236';
        }
      }

      &.focus,
      &.opened{
        > a{
          background: $rd-navbar-nav-hover-background;

          .overlay-skew {
            &:before {
              animation: in .3s forwards;
            }
          }
        }

        > .rd-navbar-submenu-toggle{
          color: $rd-navbar-nav-hover-color;
        }
      }

      &.active {
        > a {
          background: $rd-navbar-nav-active-background;

          .overlay-skew {
            &:before {
              animation: in .3s forwards;
            }
          }
        }

        > .rd-navbar-submenu-toggle{
          color: $rd-navbar-nav-active-color;
        }
      }
    }
  }

  // RD Navbar Inner
  .rd-navbar-inner {
    position: relative;
    margin-left: auto;
    margin-right: auto;
  }

  // RD Navbar top panel
  .rd-navbar-top-panel{
    background-color: rgba($gray-darker, .9);

    &-inner{
      max-width: $rd-navbar-width;
      padding-left: 20px;
      padding-right: 20px;
      margin-left: auto;
      margin-right: auto;
    }

    &,
    a{
      color: $white;
    }
  }

  // RD Navbar Search
  .rd-navbar-search{
    position: relative;
    display: inline-flex;

    &.active{
      .rd-search {
        visibility: visible;
        opacity: 1;
        width: 320px;
      }
    }

    // Make toggle for sidebar
    .rd-navbar-search-toggle{
      position: relative;
      display: inline-flex;
      align-items: center;

      .search-content {
        display: inline-block;
        transition: .3s color ease-in-out;
      }

      @include toggle-icons-via-rotation(20px, 24px, 18px, '\f43b', '\f24c', 'Material Design Icons', 'Material Design Icons');
    }

    .form-wrap{
      margin-bottom: 0;
    }


    .form-input{
      min-height: 38px;
      padding-top: 6px;
      padding-bottom: 6px;
      padding-right: 50px;
    }

    .form-label {
      color: $gray-darker;
    }

    .rd-search{
      position: absolute;
      top: -7px;
      right: 0;
      width: 100px;
      opacity: 0;
      visibility: hidden;

      transition: .3s;
      z-index: 2;
    }

    .rd-search-form-submit{
      position: absolute;
      top: 0;
      bottom: 0;
      right: 0;

      width: 50px;
      padding: 0;
      border: none;

      background-color: transparent;
      color: $gray-base;
      transition: 300ms all ease-in-out;

      &:hover{
        color: $primary;
      }
    }
  }

  // RD Navbar Dropdown
  .rd-navbar-nav {
    > li > .rd-navbar-dropdown {
      position: absolute;
      left: -5px;

      width: $rd-navbar-dropdown-width;
      background: $rd-navbar-dropdown-background;
      z-index: 5;
    }

    li.focus,
    li.opened {
      > .rd-navbar-dropdown,
      > .rd-navbar-megamenu {
        opacity: 1;
        visibility: visible;
      }

      > .rd-navbar-dropdown {
        transform: translateY(0);
      }

      > .rd-navbar-megamenu {
        transform: translate(-50%, 0);
      }
    }

    > li {
      > .rd-navbar-dropdown,
      > .rd-navbar-megamenu {
        z-index: 15;
      }

      > .rd-navbar-dropdown {

        .rd-navbar-dropdown {
          left: 100%;
          top: 0;
          margin-top: -8px;
          margin-left: 8px;
          z-index: 2;
          transform: translate(30px, 0);
        }

        > li.focus,
        > li.opened {
          > .rd-navbar-dropdown {
            opacity: 1;
            visibility: visible;
            transform: translate(0, 0);
          }
        }
      }
    }
  }

  .rd-navbar-dropdown {
    transform: translateY(30px);
  }

  .rd-navbar-megamenu {
    transform: translate(-50%, 30px);
  }

  .rd-navbar-dropdown,
  .rd-navbar-megamenu {
    position: absolute;

    display: block;
    padding: 8px 0;

    visibility: hidden;
    opacity: 0;

    text-align: left;
    box-shadow: $rd-navbar-shadow;
    @extend %rd-navbar-transition;

    li{
      > a{
        transition: .15s ease-in-out;

        &:before{
          content: '-';
          padding-right: 6px;
          margin-left: -14px;
          transition: opacity .15s ease-in-out, .15s margin ease-in-out;
          opacity: 0;
        }
      }

      &.focus > a,
      &.opened > a,
      > a:hover{
        &:before{
          margin-left: -6px;
          opacity: 1;
        }
      }
    }
  }

  // RD Navbar Dropdown
  .rd-navbar-dropdown {
    padding: 35px 20px 40px 40px;
    width: $rd-navbar-dropdown-width;
    background: $rd-navbar-dropdown-background;

    > li{
      > a {
        display: block;
        padding: 0;
        font-size: 18px;
        line-height: 2;
        color: $rd-navbar-dropdown-item-color;
        background: $rd-navbar-dropdown-item-background;

        &:hover{
          background: $rd-navbar-dropdown-item-hover-background;
        }
      }

      &.focus,
      &.opened{
        > a{
          color: $rd-navbar-dropdown-item-active-color;
          background: $rd-navbar-dropdown-item-active-background;
        }
      }
    }
  }

  // RD Navbar Megamenu
  .rd-navbar-megamenu {
    left: 50%;

    display: flex;
    width: 98%;
    max-width: 1200px;
    padding: 24px 40px 40px;
    border-top: 4px solid $white;

    background: $rd-navbar-megamenu-background;

    > li {
      width: 25%;

      // Megamenu column
      > ul {
        li + li {
          margin-top: 1px;
        }

        // Megamenu link
        a {
          display: inline-block;
          color: $rd-navbar-megamenu-item-color;
          background: $rd-navbar-megamenu-item-background;
          font-size: 14px;
          line-height: 2.14;
          font-weight: 600;

          &:hover {
            background: $rd-navbar-megamenu-item-hover-background;
          }
        }
      }

      // Megamenu column gutter
      & + li {
        padding-left: 20px;
      }
    }
  }

  // RD Navbar Clone
  &.rd-navbar--is-clone {
    display: block;
    transform: translateY(-100%);

    &.rd-navbar--is-stuck {
      transform: translateY(0%);
    }
  }

  // RD Navbar Stuck
  &.rd-navbar--is-stuck,
  &.rd-navbar--is-clone {
    position: fixed;
    left: 0;
    top: 0;
    right: 0;
    z-index: 999;
    background: $rd-navbar-background;

    .rd-navbar-top-panel{
      display: none;
    }
  }

  // RD Navbar States
  .rd-navbar--has-dropdown {
    position: relative;
  }

  .rd-navbar-collapse {
    > * {
      display: inline-block;
    }
  }

  .forms-modal { text-align: center; }

  .rd-megamenu-list {
    padding-left: 15px;
  }
}


//=============      Fixed & Sidebar      ================
.rd-navbar-fixed,
.rd-navbar-sidebar {


}


// Scrollbar styles

.rd-navbar-fixed .rd-navbar-nav-wrap {
  width: $rd-navbar-nav-min-width;

  &:before,
  &:after {
    content: '';
    display: block;
    height: $rd-navbar-min-height;
  }
}

.rd-navbar-sidebar .rd-navbar-sidebar-inner  {
  width: 309px;
}

.rd-navbar-fixed .rd-navbar-nav-wrap,
.rd-navbar-sidebar .rd-navbar-sidebar-inner  {
  height: 100%;
  left: 0;
  top: 0;
  overflow-y: auto;
  overflow-x: hidden;
  -webkit-overflow-scrolling: touch;
  font-size: 16px;
  line-height: 34px;
  color: $rd-navbar-color;
  background: $rd-navbar-fixed-panel-background;
  box-shadow: $rd-navbar-fixed-shadow;
  z-index: 998;

  &::-webkit-scrollbar {
    width: 4px;
  }

  &::-webkit-scrollbar-thumb {
    border: none;
    border-radius: 0;
    opacity: .2;
  }

  &::-webkit-scrollbar-track {
    border: none;
    border-radius: 0;
  }
}

.rd-navbar-fixed .rd-navbar-nav,
.rd-navbar-sidebar .rd-navbar-nav {


  // RD Navbar Dropdown
  .rd-navbar-dropdown {
    > li > a {
      padding-left: 20px;
    }

    ul > li > a {
      padding-left: 25px;
    }
  }
}

.rd-megamenu-header + * {
  margin-top: 8px;
}

.rd-megamenu-header {
  font-size: 1.28em;
  line-height: 1.33;
}

@import "rd-navbar_includes/_rd-navbar-static";
@import "rd-navbar_includes/_rd-navbar-fullwidth";
@import "rd-navbar_includes/_rd-navbar-fixed";
@import "rd-navbar_includes/_rd-navbar-sidebar";
@import "rd-navbar_includes/_rd-navbar-default";
@import "rd-navbar_includes/_rd-navbar-variant-2";


ul ul,
ul ol,
ol ul,
ol ol {
  padding-left: 0;
}

.rd-navbar-sidebar-linked {
  body {
    .page {

      @media (min-width: 1600px) {
        padding-left: 309px;
      }
    }
  }
}

.rd-navbar-top-panel {
  .search_title {
    a {
      @include link($gray-darker, $primary);
    }
  }

  .search_all {

    a {
      color: $gray-darker;
    }
  }
}