// Contexts
// -------------------------



.context-dark {
  @extend %context-dark;
}

.context-light {
  @extend %context-light;
}

// bg-behaviour() off padding top if two section with same bg that are beside
//
.bg-white {
  @include bg-behaviour($white);
}

.bg-primary {
  @extend %context-dark;
  @include bg-behaviour($primary);
  
  .post-content {
    
    h1 {
      a { 
        &:hover {
          color: $gray-darker;
        }
      }
    }
  }

  .post-minimal-2 {
    h1 {
      a {
        &:hover {
          color: $gray-darker;
        }
      }
    }
  }
}

.bg-primary-sec {
  @extend %context-dark;
  @include bg-behaviour($primary-sec);
}

.bg-gray-dark {
  @extend %context-dark;
  @include bg-behaviour($gray-dark);
}

.bg-gray-darker {
  color: inherit;
  @extend %context-dark;
  @include bg-behaviour($gray-darker);

  h6 + .rd-mailform { margin-top: 20px; }
}

.bg-gray-2 {
  @include bg-behaviour($gray-2);
}

.bg-gray-lighter {
  @include bg-behaviour($gray-lighter);
}

// Background image
//
.bg-image {
  background-size: cover;
  background-position: center center;
  background-repeat: no-repeat;
}

.bg-fixed {
  .desktop & {
    @media (min-width: $screen-md-min) {
      background-attachment: fixed;
    }
  }
}

// Background image

.bg-home-1 {
  background-image: url(../images/bg-home-1-1920x377.jpg);
}

.bg-home-2 {

  &:after {
    position: absolute;
    display: none;
    content: '';
    top: 0;
    bottom: 58px;
    right: 0;
    background-size: cover;
    background-position: center center;
    background-repeat: no-repeat;
    background-image: url(../images/bg-home-2-866x825.png);
    z-index: -1;
    
    @media (min-width: $screen-md-min) {
      display: block;
      left: 50%;
    }

    @media (min-width: 1400px) {
      bottom: 56px;
      left: calc(50% + 95px);
      background-position-x: 0;
    }
  }
}

.bg-home-4 {

  @media (min-width: $screen-sm-min) {
    background: $gray-2 url(../images/bg-home-4-1920-399.jpg) no-repeat 55% 50%;
    background-size: cover;
  }

  @media (min-width: $screen-lg-min) {
    background-position: center center;
  }
}

.bg-home-5 {
  background-image: url(../images/bg-home-5-1920-532.jpg);
  background-position-x: 50%;

  @media (min-width: $screen-sm-min) {
    background-position-x: 60%;
  }
}

.bg-home-6 {

  @media (min-width: $screen-sm-min) {
    position: absolute;
    content: '';
    top: 0;
    right: 0;
    bottom: 0;
    left: 45%;
    will-change: transform;
    transform: skewX(-5deg);
    transform-origin: 100% 100%;
    outline: 1px solid transparent;
    -webkit-backface-visibility: hidden;
    overflow: hidden;

    &:before {
      position: absolute;
      content: '';
      top: 0;
      right: -30px;
      bottom: 0;
      left: -30px;
      background-repeat: no-repeat;
      background-position: 50%;
      background-size: cover;
      background-image: url(../images/bg-home-6-982x473.jpg);
      will-change: transform;
      transform: skewX(5deg);
      transform-origin: 50% 50%;
    }
  }
  
  @media (min-width: $screen-md-min) {
    left: 50%;
  }

  @media (min-width: $screen-lg-min) {
    &:before {
      background-position-x: 40%;
    }
  }

  @media (min-width: $screen-xlg-min) {
    left: 650px;
  }
}

.bg-home-7 {

  @media (max-width: $screen-xs-max) {
    background-repeat: no-repeat;
    background-position: 50%;
    background-size: cover;
    background-image: url(../images/bg-home-6-982x473.jpg);
  }
}

.bg-image-1 {
  background-image: url(../images/bg-image-1-1920x721.jpg);
}

.bg-overlay-primary-sec {
  
  &:before {
    position: absolute;
    content: '';
    top: 0;
    right: 0;
    bottom: 90px;
    left: 0;
    background: $primary-sec;
    z-index: -1;
  }

  @media (min-width: $screen-lg-min) {
    &:before {
      bottom: 120px;
    }
  }
}

.bg-banner {
  position: relative;
  
  &:before {
    position: absolute;
    content: '';
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    z-index: -1;
    transition: .3s all ease;
  }
}

.bg-banner-primary {

  &:before {
    background: $primary;
  }
}

.bg-banner-primary-sec {

  &:before {
    background: $primary-sec;
  }
}

