.quote-wrap {
  padding: 24px 15px 30px;
  background: $gray-darker;

  q {
    display: inline-block;

    &:before,
    &:after {
      content: '';
    }
  }

  div span,
  cite {
    font-family: $font-family-sec;
    font-weight: 700;
    color: $gray-dark;
    display: block;
  }

  cite {
    color: $primary;
    font-weight: 700;
    font-style: normal;
  }

  @media (min-width: $screen-sm-min) {
    padding-right: 18px;
    padding-left: 20px;
  }
}

.quote {
  position: relative;
  padding-left: 35px;
  text-align: left;

  &:before {
    position: absolute;
    top: 0;
    left: 0;
    content: '\f10d';
    color: $white;
    font: 22px/1 'FontAwesome';
  }

  p + p { margin-top: 18px; }

  @media (min-width: $screen-sm-min) {
    padding-left: 48px;

    &:before {
      font-size: 36px;
    }
  }
}