/*
*
* Circle Progress Bars
* --------------------------------------------------
*/


.progress-bar-circle {
  display: inline-block;
  position: relative;
  text-align: center;
  color: $gray-darker;
  font-family: $font-family-sec;
  line-height: 1.2;

  canvas {
    vertical-align: middle;
  }

  .wrap-progress-bar-circle {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
  }

  span {
    font-size: 48px;
    line-height: 1;
    letter-spacing: -0.025em;
    font-weight: 700;

    &:after {
      content: "%";
    }
  }

  .progress-title {
    font-size: 18px;
    line-height: 24px;
    letter-spacing: -0.010em;
  }

  * + p {
    margin-top: 0;
  }
}
