/*
*
* Pagination custom
* --------------------------------------------------
*/

//== Pagination variables
//

$pagination-padding-vertical: 0;
$pagination-padding-horizontal: 10px;

$pagination-font-size: 18px;
$pagination-lh-size: 24px;
$pagination-border-w: 2px;

$pagination-color:                     $primary;
$pagination-bg:                        transparent;
$pagination-border:                    #ddd;

$pagination-hover-color:               $gray-light;
$pagination-hover-bg:                  transparent;
$pagination-hover-border:              #ccc;

$pagination-active-color:              $gray-light;
$pagination-active-bg:                 transparent;
$pagination-active-border:             $primary;

$pagination-disabled-color:            rgba($gray-light, .5);
$pagination-disabled-bg:               transparent;
$pagination-disabled-border:           #ddd;


.pagination-custom {
  font-size: 0;

  > li {
    margin: 0 3px;

    &,
    > a,
    > span{
      display: inline-block;
      vertical-align: middle;
    }

    > a,
    > span {
      padding: $pagination-padding-vertical $pagination-padding-horizontal;
      font-size: $pagination-font-size;
      line-height: $pagination-lh-size;
    }
  }

  > li > a,
  > li > span {
    min-width: $pagination-padding-vertical*2 + $pagination-border-w*2 + $pagination-lh-size;
    font-weight: 700;
    transition: .3s;
    text-align: center;
    background-color: transparent;
    color: $pagination-color;
    border: none;
    //box-shadow: 0 2px 6px 0 rgba(0, 0, 0, 0.10);
  }

  > li:first-child,
  > li:last-child {

    @media (max-width: $screen-xs-min - 1) {
      display: none;
    }

    > a, > span {

      color: $gray-light;

      &:hover,
      &:focus,
      &:active,
      &:active:focus {
        color: $primary;
      }

      span {
        position: relative;
        top: 2px;
        display: inline-block;
        vertical-align: middle;
        font-size: 24px;
        line-height: 1;
      }
    }
  }

  > li.disabled {

    > a, > span {

      color: $pagination-disabled-color;
      pointer-events: none;
    }
  }

  > li:last-child {
    a span,
    span span {
      transform: scale(-1, 1);
    }
  }

  > li > a,
  > li > span {
    &:hover,
    &:focus,
    &:active,
    &:active:focus {
      color: $pagination-hover-color;
      background-color: $pagination-hover-bg;
      border-color: $pagination-hover-border;
      box-shadow: none;
    }
  }

  > .active > a,
  > .active > span {
    &,
    &:hover,
    &:focus,
    &:active,
    &:active:focus {
      color: $pagination-active-color;
      background-color: $pagination-active-bg;
      border-color: $pagination-active-border;
      box-shadow: none;
    }
  }
}

