/*
*
* Element groups
* --------------------------------------------------
*/

html .page {
  .group {
    @include group(15px, 20px);
  }

  .group-xs {
    @include group(12px, 5px);

    .list-tags {
      margin-left: 6px;
    }
  }

  .group-sm {
    @include group(14px, 10px);
  }

  .group-md {
    @include group(20px, 10px);
  }

  .group-lg {
    @include group(22px, 16px);
  }

  .group-xl {
    @include group(35px, 15px);
  }

  .group-post-panel {
    @include group(20px, 10px);
  }

  .group-flickr {
    @include group(4px, 4px);
  }

  .group-partners {
    @include group(26px, 16px);
  }

  .group-buttons {
    @include group(5px, 15px);

    &.align-bottom {
      > * {
        vertical-align: bottom;
      }
    }
  }

  .group-buttons-2 {
    @include group(15px, 15px);
  }

  .group-buttons-3 {
    @include group(10px, 15px);
  }

  .group-contacts {
    @include group(80px, 16px);
  }

  .group-top {
    > *, > *:first-child {
      vertical-align: top;
    }
  }

  .group-center {
    > *, > *:first-child {
      vertical-align: middle;
    }
  }

  .group-bottom {
    > *, > *:first-child {
      vertical-align: bottom;
    }
  }
}

.group-middle {
  @include display-flex();
  @include align-items(center);
  @include flex-direction(row);
  @include flex-wrap(wrap);
  @include justify-content(space-between);
}

.group-md-justify {

  @media (min-width: $screen-md-min) {
    @include display-flex;
    @include flex-direction(row);
    @include align-items(center);
    @include justify-content(center);

    > * {
      @include flex-grow(1);
      @include flex-shrink(0);
    }

    .button {
      min-width: 148px;
    }
  }
}