/*
* Fullwidth Layout
*/

.rd-navbar-fullwidth {
  display: block;

  // RD Navbar Inner
  .rd-navbar-inner{
  }

  // RD Navbar right aside
  .rd-navbar-aside-right{
    display: flex;
    align-items: center;
    justify-content: center;
    margin-top: 10px;
  }

  // RD Navbar Nav
  .rd-navbar-nav {
    > li + li {
      margin-left: $navbar-fullwidth-nav-indent;
    }

    &:nth-child(4) { margin-left: -5px; }
  }

  // RD Navbar States
  &.rd-navbar--is-stuck,
  &.rd-navbar--is-clone{
    .rd-navbar-inner{
    }

    .rd-navbar-aside-right{
      margin-top: 0;
    }

    .rd-navbar-panel{

    }
  }
}