/*
*
* Tabs
* --------------------------------------------------
*/


//== Tabs variables
//
//##

// Base
$tabs-color: $white;
$tabs-background: transparent;
$tabs-active-background: $gray-darker;
$tabs-font: 700 18px/25px $font-family-sec;
$tabs-padding: 9px 40px;

// Horizontal tabs
$tabs-horizontal-active-color: $white;
$tabs-horizontal-active-background: $gray-darker;
$tabs-horizontal-background: $primary;

// Vertical tabs
$tabs-vertical-active-color: $white;
$tabs-vertical-active-line: $gray-darker;



// Base styles
//--------------------------------------

.tabs-custom {
  .nav-custom {
    &> li, &> li > a {
      display: block;
      position: relative;
    }
  }

  .nav-custom-tabs {
    font-size: 0;
    line-height: 0;

    li {
      width: 100%;
      margin: -1px 0;

      &+ li {
        margin-top: 7px;

        @media (min-width: $screen-sm-min) {
          margin-top: 0;
          margin-left: 9px;
        }
      }
    }
  }
}

.tabs-custom {
  .nav-custom-tabs {
    li a {
      display: block;
      position: relative;
      padding: $tabs-padding;
      font: $tabs-font;
      color: $tabs-color;
      background: $tabs-background;
      border: none;
      text-align: center;
      vertical-align: middle;
      transition: .3s all ease-in-out;
      z-index: 3;
    }

    li {

      span {
        position: absolute;
        overflow: hidden;
        top: 0;
        right: 0;
        bottom: 0;
        left: 0;
        will-change: transform;
        z-index: 1;
        background: $primary;
        transform: skewX(-6deg);
        outline: 1px solid transparent;
        -webkit-backface-visibility: hidden;

        &:before {
          position: absolute;
          content: '';
          top: 0;
          right: 0;
          bottom: 0;
          will-change: transform;
          transition: .3s all ease-in-out;
          z-index: -1;
          left: calc(100% - 30px);
          transform: skewX(22deg);
          transform-origin: 0 0;
          outline: 1px solid transparent;
          -webkit-backface-visibility: hidden;
          background: $tabs-vertical-active-line;
        }
      }
    }

    li:hover > span,
    li.active > span,
    li a.active + span {

      &:before {
        left: 0;
      }
    }
  }


}

.tab-content {
  padding-top: 20px;

  @media (min-width: $screen-sm-min) {
    padding-top: 32px;
  }
}


// Horizontal tabs
//--------------------------------------

.tabs-custom {
  &.tabs-horizontal {
    @media (min-width: $screen-sm-min) {
      .nav-custom-tabs {
        position: relative;
        @include justify-content(flex-start);
        @include display-flex;

        li { width: auto; }

        li span:before{
          left: calc(100% - 25px);
        }

        li:hover > span,
        li.active > span,
        li a.active + span {

          &:before {
            left: 0;
          }
        }

        li a {
          padding: 8px 15px 14px;
          min-width: 110px;
        }
      }
    }
  }

  &.isotope-filters {
    &.tabs-horizontal {

      @media (min-width: $screen-sm-min) {
        .nav-custom-tabs {
          li a {
            padding-left: 25px;
            padding-right: 25px;
          }
        }
      }
    }

    @media (min-width: $screen-sm-min) {

      .nav-custom-tabs {
        @include flex-wrap(wrap);
        margin-left: -9px;
        margin-bottom: -9px;
        transform: translateY(-9px);

        li {
          margin-left: 9px;
          margin-top: 9px;
        }
      }
    }
  }
}


// Vertical tabs
//--------------------------------------

.tabs-custom {
  &.tabs-vertical {
    @media (min-width: $screen-sm-min) {
      @include display-flex;
      @include flex-direction(row);
      @include flex-wrap(nowrap);
      @include align-items(stretch);

      .nav-custom-tabs {
        @include display-flex;
        @include flex-direction(column);
        @include flex-wrap(nowrap);
        @include align-items(stretch);
        @include flex-shrink(0);
        @include justify-content(flex-start);
        max-width: 50%;
      }

      .tab-content {
        @include flex-grow(1);
      }

      .nav-custom-tabs {
        text-align: right;
        border-right: none;

        li + li {
          margin-top: 8px;

          @media (min-width: $screen-sm-min) {
            margin-left: 0;
          }
        }

        li span:before {
          left: calc(100% - 30px);
        }

        li:hover > span,
        li.active > span,
        li a.active + span {

          &:before {
            left: 0;
          }
        }

        li a {
          padding: 8px 28px 14px;

          @media (min-width: $screen-sm-min) {
            text-align: left;
          }
        }
      }

      .tab-content {
        padding: 0 0 0 30px;
      }
    }

    .nav-custom-tabs {
      @media (min-width: $screen-sm-min) {
        min-width: 150px;
      }
      
      @media (min-width: $screen-md-min) {
        min-width: 200px;
      }

      @media (min-width: $screen-lg-min) {
        min-width: 260px;
      }
    }
    
  }
}

.page {
  .tabs-custom-wrap + .range {
    margin-top: 0;
  }
}

.tab-product {

  .nav-custom {

    @include display-flex();
    @include flex-wrap(wrap);
    align-items: stretch;
  }

  .tab-content {
    position: relative;
    padding-top: 0;
  }

  .tab-img {
    max-width: 33.33%;
    @include flex-basis(33.33%);
  }

  li {
    position: relative;
    overflow: hidden;
    will-change: transform;
  }

  .tab-img-1,
  .tab-img-2,
  .tab-img-3 {
    outline: 1px solid transparent;
    -webkit-backface-visibility: hidden;
  }
}

.tab-product-var-1 {

  .nav-custom {
    margin-left: -24px;
    margin-right: -5px;
  }

  .tab-img-1,
  .tab-img-2,
  .tab-img-3 {
    transform: skew(-8deg);
    transform-origin: 100% 100%;


    img {
      transform: skew(8deg);
      transform-origin: 100% 100%;
      width: 115%;
      max-width: 115%;
    }
  }

  .tab-content {
    img {
      width: 100%;
    }
  }

  p.big {
    line-height: 1.33;
  }

  @media (min-width: $screen-xs-min) {
    .nav-custom {
      margin-left: -40px;
    }
  }

  @media (min-width: $screen-sm-min) {
    .nav-custom {

      margin-left: -28px;

      img {
        height: 100%;
      }
    }

    .tab-img {
      height: 200px;
      max-height: 200px;
      max-width: 33.33%;
      @include flex-basis(33.33%);
    }
  }

  @media (min-width: $screen-md-min) {
    .nav-custom {
      margin-left: -36px;
    }

    .tab-img {
      height: 260px;
      max-height: 260px;
    }
  }

  @media (min-width: $screen-lg-min) {

    .tab-content {
      img {
        height: 680px;
        max-height: 680px;
      }
    }

    .nav-custom {
      margin-left: -48px;

      > li {
        max-width: 50%;
        @include flex-basis(50%);
      }
    }

    .tab-pane {
      margin-left: -100px;
    }

    li[class*='tab-'] {
      height: 340px;
      max-height: 340px;
    }

    .tab-img-3 {
      margin-left: 0;
      transform-origin: 0 0;
    }
  }

  @media (min-width: $screen-xlg-min) {

    .tab-content {
      img {
        height: 830px;
        max-height: 830px;
      }
    }

    .nav-custom {
      margin-left: -60px;
    }

    .tab-pane {
      margin-left: 0;
    }

    li[class*='tab-'] {
      height: 415px;
      max-height: 415px;
    }
  }

  @media (min-width: $screen-xl-min) {
    .nav-custom {
      margin-left: -77px;
    }
  }

  @media (min-width: 2000px) {

    .tab-content {
      min-height: 100%;

      img {
        min-height: 100%;
        max-height: none;
      }
    }

    li[class*='tab-'] {
      height: auto;
      max-height: none;
    }

    .nav-custom {
      margin-left: -150px;
    }

    .tab-pane {
      position: absolute;
      top: 0;
      right: 0;
      bottom: 0;
      left: 0;
    }
  }
}

.tab-product-var-2 {
  .tab-img-1,
  .tab-img-2,
  .tab-img-3 {
    transform: skew(-6deg);
    transform-origin: 0 0;

    img {
      transform: skew(6deg);
      transform-origin: 100% 100%;
      max-width: 110%;
    }
  }
  
  .nav-custom {
    margin-right: -16px;
  }

  .tab-pane {
    position: relative;
    overflow: hidden;

    img {
      max-width: 180%;
      min-height: 350px;
      margin-left: -70px;
    }
  }

  @media (min-width: $screen-xs-min) {
    .tab-pane {

      img {
        max-width: 140%;
        margin-left: -70px;
      }
    }

    .nav-custom {
      margin-right: -22px;
    }
  }

  @media (min-width: $screen-sm-min) {
    .tab-pane {
      transform: skew(-6deg);
      transform-origin: 100% 100%;

      img {
        margin-left: 0;
        max-width: 120%;
        transform: skew(6deg);
        transform-origin: 100% 100%;
      }
    }

    .nav-custom {
      margin-right: -3px;
    }
  }
  
  @media (min-width: $screen-md-min) {
    .nav-custom {
      margin-right: -2px;
    }  
  }
}

.tab-img {

  @media (min-width: $screen-sm-min) {
    a {

      &:before {
        position: absolute;
        content: '';
        top: 0;
        right: 0;
        bottom: 0;
        left: 0;
        background: $gray-base;
        opacity: .4;
        transition: 300ms ease-in-out;
        z-index: 1;
      }
    }

    &.active,
    &:hover {
      a {
        &:before {
          opacity: 0;
        }
      }
    }
  }
}

.tab-img-2,
.tab-img-3 {
  margin-left: -1px;
}

.product-name {
  position: absolute;
  top: 0;
  left: 50%;
  max-width: 400px;
  padding: 15px;
  width: 100%;
  transform: translateX(-50%);

  h6 {
    font-weight: 400;
  }

  * + p { margin-top: 20px; }

  h1 + h6 {
    margin-top: 0;
  }

  @media (min-width: $screen-xs-min) {
    padding-top: 50px;
  }

  @media (min-width: $screen-sm-min) {
    * + p { margin-top: 35px; }
  }
}

.product-descr {
  position: absolute;
  top: 50%;
  left: 50%;
  max-width: 540px;
  padding: 15px;
  width: 100%;
  transform: translate(-50%, -50%);

  * + .button {
    margin-top: 20px;
  }
}

.tab-descr {
  font-size: 12px;
  padding: 40px 15px 40px 29px;

  .big {
    font-size: 14px;
  }

  p.big + p { margin-top: 16px; }

  * + p { margin-top: 12px; }
  * + .button { margin-top: 20px; }

  @media (min-width: $screen-xs-min) {
    padding-left: 35px;
  }

  @media (min-width: $screen-sm-min) {
    padding-left: 48px;
  }

  @media (min-width: $screen-md-min) {
    padding-left: 56px;
  }

  @media (min-width: $screen-lg-min) {
    padding: 20px 15px 15px 20px;
  }

  @media (min-width: $screen-xlg-min) {
    font-size: 14px;
    padding: 25px 20px 20px 25px;

    .big {
      font-size: 18px;
    }

    * + p { margin-top: 20px; }

    * + .button { margin-top: 35px; }
  }

  @media (min-width: $screen-xl-min) {
    padding: 45px 30px 30px 45px;
  }
}