/*
* Static Layout
*/

.rd-navbar-static {
  display: block;

  .rd-navbar-inner{
    display: flex;
    align-items: center;
  }

  .rd-navbar-aside-right{
    display: flex;
  }

  .rd-navbar-top-panel{
    text-align: left;
  }

  .rd-navbar-nav{
    > li {
      & + li{
        margin-left: $navbar-static-nav-indent;
      }

      &:nth-child(4) { margin-left: -5px; }
    }
  }

  &.rd-navbar--is-stuck,
  &.rd-navbar--is-clone{
    .rd-navbar-inner{

    }
  }
}

