//
// Breadcrumbs
// --------------------------------------------------
$breadcrumb-padding-vertical:   8px;
$breadcrumb-padding-horizontal: 15px;
$breadcrumb-bg:                 $white;
$breadcrumb-color:              $white;
$breadcrumb-active-color:       $white;
$breadcrumb-separator:          "/";

.breadcrumbs-custom {
  position: relative;
  vertical-align: middle;

  a {
    display: inline;
    vertical-align: middle;
    &, &:active, &:focus {
      color: $breadcrumb-color;
    }
  }

  li {
    position: relative;
    display: inline-block;
    vertical-align: middle;
    padding-right: 24px;
    font-size: 14px;
  }

  li:after {
    content: $breadcrumb-separator;
    position: absolute;
    top: 50%;
    right: 7px;
    display: inline-block;
    color: $white;
    transform: translateY(-50%);
  }

  li:last-child {
    padding-right: 0;
    &:after {
      display: none;
    }
  }

  a:hover {
    color: $primary;
  }

  li.active {
    color: $breadcrumb-active-color;
  }
}

* + .breadcrumbs-custom {
  margin-top: 13px;
}

@media (min-width: $screen-sm-min) {
  .breadcrumbs-custom {
    li {
      font-size: 18px;
    }

    li:after {
      font-size: 18px;
    }
  }
}
