/*
*
* Owl Carousel
* --------------------------------------------------
*/

.owl-carousel .animated {
  -webkit-animation-duration: 1000ms;
  animation-duration: 1000ms;
  -webkit-animation-fill-mode: both;
  animation-fill-mode: both;
}

.owl-carousel .owl-animated-in {
  z-index: 0;
}

.owl-carousel .owl-animated-out {
  z-index: 1;
}

.owl-carousel .fadeOut {
  -webkit-animation-name: fadeOut;
  animation-name: fadeOut;
}

@-webkit-keyframes fadeOut {
  0% {
    opacity: 1;
  }

  100% {
    opacity: 0;
  }
}

@keyframes fadeOut {
  0% {
    opacity: 1;
  }

  100% {
    opacity: 0;
  }
}

/* 
 * 	Owl Carousel - Auto Height Plugin
 */
.owl-height {
  transition: height 500ms ease-in-out;
}

/* 
 *  Core Owl Carousel CSS File
 */
.owl-carousel {
  display: none;
  width: 100%;
  -webkit-tap-highlight-color: transparent;
  /* position relative and z-index fix webkit rendering fonts issue */
  position: relative;
  z-index: 1;
}

.partners-carousel {

  .owl-stage-outer {
    padding: 10px 0 40px;
  }

  .owl-stage {
    @include display-flex();
    @include align-items(center);
  }

  .owl-item {
    img {
      will-change: transform;
      transform: translateY(0);
      transition: inherit;

      &:hover {
        transform: translateY(-10px);
      }
    }
  }

  @media (min-width: 1800px) {
    .owl-stage-outer {
      padding-bottom: 10px;
    }
  }
}

.owl-carousel .owl-stage {
  position: relative;
  -ms-touch-action: pan-Y;
}

.owl-carousel .owl-stage:after {
  content: ".";
  display: block;
  clear: both;
  visibility: hidden;
  line-height: 0;
  height: 0;
}

.owl-carousel .owl-stage-outer {
  position: relative;
  overflow: hidden;
  /* fix for flashing background */
  -webkit-transform: translate3d(0px, 0px, 0px);
}

.owl-carousel .owl-controls .owl-nav .owl-prev,
.owl-carousel .owl-controls .owl-nav .owl-next,
.owl-carousel .owl-controls .owl-dot {
  cursor: pointer;
  user-select: none;
}

.owl-carousel.owl-loaded {
  display: block;
}

.owl-carousel.owl-loading {
  opacity: 0;
  display: block;
}

.owl-carousel.owl-hidden {
  opacity: 0;
}

.owl-carousel .owl-refresh .owl-item {
  display: none;
}

.owl-skew {

  &:before,
  &:after {
    position: absolute;
    content: '';
    top: 0;
    bottom: 0;
    width: 32px;
    background: $white;
    transform: skewX(-5deg);
    z-index: 1;
  }
  
  &:before {
    left: 0;
    transform-origin: 0 0;
  }
  
  &:after {
    right: 0;
    transform-origin: 100% 100%;
  }

  .owl-prev,
  .owl-next {
    z-index: 2;
  }
  
  @media (min-width: $screen-xs-min) {
    &:before,
    &:after {
      transform: skewX(-6deg);
    }
  }
}

.owl-carousel .owl-item {
  position: relative;
  min-height: 1px;
  float: left;
  -webkit-backface-visibility: hidden;
  -webkit-tap-highlight-color: transparent;
  -webkit-touch-callout: none;

  > .owl-item {
    width: 100%;
  }
}

.owl-carousel .owl-grab {
  cursor: move;
  cursor: -webkit-grab;
  cursor: grab;
}

.owl-carousel.owl-rtl {
  direction: rtl;
}

.owl-carousel.owl-rtl .owl-item {
  float: right;
}

/* No Js */
.no-js .owl-carousel {
  display: block;
}

/* 
 * 	Owl Carousel - Lazy Load Plugin
 */
.owl-carousel .owl-item .owl-lazy {
  opacity: 0;
  transition: opacity 400ms ease;
}

/* 
 * 	Owl Carousel - Video Plugin
 */
.owl-carousel .owl-video-wrapper {
  position: relative;
  height: 100%;
  background: #000;
}

.owl-carousel .owl-video-play-icon {
  position: absolute;
  height: 80px;
  width: 80px;
  left: 50%;
  top: 50%;
  margin-left: -40px;
  margin-top: -40px;
  font: 400 40px/80px 'FontAwesome';
  cursor: pointer;
  z-index: 1;
  -webkit-backface-visibility: hidden;
  transition: scale 100ms ease;
  &:before {
    content: '\f144';
  }
}

.owl-carousel .owl-video-play-icon:hover {
  -webkit-transform: scale(1.3);
  transform: scale(1.3);
}

.owl-carousel .owl-video-playing .owl-video-tn,
.owl-carousel .owl-video-playing .owl-video-play-icon {
  display: none;
}

.owl-carousel .owl-video-tn {
  opacity: 0;
  height: 100%;
  background-position: center center;
  background-repeat: no-repeat;
  background-size: contain;
  transition: opacity 400ms ease;
}

.owl-carousel .owl-video-frame {
  position: relative;
  z-index: 1;
}

/*
 * Owl Navigation
 */

.owl-nav {

}

.owl-prev, .owl-next {
  position: absolute;
  top: 50%;
  width: 40px;
  text-align: center;
  font: 400 30px/48px 'Material Icons';
  transition: 300ms ease-in-out all;
}

.owl-prev {

  &:before {
    content: "\e317";
  }
}

.owl-next {

  &:before {
    content: "\e317";
  }
}

.owl-nav-variant-1 {

  .owl-prev, .owl-next {
    color: $gray-base;

    &:after {
      position: absolute;
      content: '';
      top: 0;
      right: 0;
      bottom: 0;
      left: 0;
      background: $white;
      z-index: -1;
      transition: 300ms ease-in-out all;
    }

    &:hover {
      color: $white;

      &:after {
        background: $primary;
      }
    }

    @media (min-width: $screen-xs-min) {
      width: 45px;
    }
  }

  .owl-prev {
    left: 3px;
    transform: translateY(-50%);

    &:after {
      transform: skewX(-6deg);
    }
  }

  .owl-next {
    right: 3px;
    transform: scale(-1, 1) translateY(-50%);

    &:after {
      transform: skewX(6deg);
    }
  }

  @media (min-width: $screen-xs-min) {
    .owl-prev {
      left: -5px;
    }

    .owl-next {
      right: -5px;
    }
  }
}

.owl-nav-variant-2 {

  .owl-prev, .owl-next {
    top: 40%;
    font-size: 36px;
    color: $gray-light;
    display: none;

    &:hover {
      color: $primary;
    }
  }

  .owl-prev {
    left: -60px;
    transform: translateY(-50%);
  }

  .owl-next {
    right: -60px;
    transform: scale(-1, 1) translateY(-50%);
  }

  .owl-dots {
    bottom: -40px;
  }

  .owl-dot {
    background-color: $gray-light;

    &:hover,
    &:focus {
      background-color: $primary;
    }

    &.active {
      background-color: $primary;
    }
  }

  @media (min-width: $screen-xs-min) {

    .owl-prev, .owl-next {
      display: block;
    }

    .owl-dots {
      display: none !important;
    }
  }
}

.owl-nav-variant-3 {

  .owl-dots {
    @media (min-width: $screen-sm-min) {
      display: none !important;
    }
  }

  .owl-dot {
    background-color: rgba($white, .6);

    &:hover,
    &:focus {
      background-color: $white;
    }

    &.active {
      background-color: $white;
    }
  }
}

.owl-nav-variant-4 {

  .owl-prev, .owl-next {
    position: absolute;
    bottom: 0px;
    top: auto;

    display: inline-block;

    font-size: 36px;
    line-height: 1;
    color: $gray-darker;

    text-align: center;
    cursor: pointer;
    transition: .2s ease-in;
    will-change: transform;
    z-index: 10;

    &:hover {
      color: $primary-sec;
    }
  }

  .owl-prev {
    left: calc(50% - 50px);

    &:after {
      position: relative;
      content: '/';
      color: $gray-light;
      right: -8px;
      top: -5px;
    }
  }

  .owl-next {
    transform: scale(-1, 1);
    right: calc(50% - 50px);
  }

  @media (min-width: 1800px) {
    .owl-prev, .owl-next {
      display: none !important;
    }
  }
}

/*
 * Owl Pagination
 */

.owl-dots {
  position: absolute;
  bottom: 20px;
  text-align: center;
  margin-top: 10px;
  width: 100%;
  z-index: 1;
}

.owl-dot {
  position: relative;

  display: inline-block;
  width: 12px;
  height: 12px;
  border-radius: 50%;
  margin-left: 8px;
  margin-right: 8px;

  text-align: center;
  outline: none;
  cursor: pointer;
  transition: .2s;
}

.owl-carousel.owl-carousel-light-dots{
  .owl-dot{
    border-color: $white;

    &:hover,
    &:focus {
      background-color: $white;
    }

    &.active {
      background-color: $white;
    }
  }
}

.custom-navigation {

  .next,
  .prev {
    display: none;
    position: absolute;
    top: 50%;
    font-size: 60px;
    line-height: 1;
    color: $white;
    cursor: pointer;
    z-index: 3;

    &:hover {
      color: $gray-darker;
    }
  }

  .next {
    right: 5px;
    transform: scale(-1, 1) translateY(-50%);
  }

  .prev {
    left: 5px;
    transform: translateY(-50%)
  }

  @media (min-width: $screen-sm-min) {
    .next,
    .prev {
      display: block;
    }
  }

  @media (min-width: $screen-md-min) {
    .next {
      right: 35px;
    }

    .prev {
      left: 35px;
    }
  }

  @media (min-width: $screen-lg-min) {
    .next {
      right: 18px;
    }

    .prev {
      left: 18px;
    }
  }
}