/*
*
* Buttons
* --------------------------------------------------
*/


// Button General Styles

.button {
  display: inline-block;
  position: relative;
  // btn-size($padding-vertical, $padding-horizontal, $font-size, $line-height, $border-radius)
  @include btn-size(9px, 30px, 14px, 25px, 0);
  min-width: 162px;
  font-weight: 700;
  font-family: $font-family-sec;
  border: none;
  white-space: nowrap;
  text-overflow: ellipsis;
  text-align: center;
  cursor: pointer;
  vertical-align: middle;
  user-select: none;
  transition: 250ms all ease-in-out;
  z-index: 1;

  @extend %buttons-styles;

  > span {
    outline: 1px solid transparent;
    -webkit-backface-visibility: hidden;
    transform: skewX(-6deg);
    z-index: -1;

    &:before {
      left: calc(100% - 30px);
      transform: skewX(22deg);
      outline: 1px solid transparent;
      -webkit-backface-visibility: hidden;
      z-index: -1;
    }
  }

  &:focus,
  &:active,
  &:active:focus {
    outline: none;
  }

  &.disabled{
    color: black;
    background-color: gray;
  }

  .caret{
    margin-left: 8px;
  }
}

.button-block{
  display: block;
  width: 100%;
}


// Appearance style
// --------------------------------------------------

.button-primary {
  @include btn-variant($white, $white, $primary, $gray-darker);
}

.button-primary-2 {
  @include btn-variant($white, $white, $primary, $primary-sec);
}

.button-primary-3 {
  @include btn-variant($white, $primary, $primary, $white);
}

.button-primary-sec {
  @include btn-variant($white, $white, $primary-sec, $gray-darker);
}

.button-primary-sec-2 {
  @include btn-variant($white, $primary-sec, $primary-sec, $white);
}

.button-white {
  @include btn-variant($primary, $primary, $white, $gray-darker);
}

.button-gray-1 {
  @include btn-variant($white, $white, $gray-1, $gray-darker);
}

.button-transparent {
  @include btn-variant($primary, $white, transparent, transparent);
}

.button-facebook {
  @include btn-variant($white, $white, $facebook, $gray-darker);
}

.button-twitter {
  @include btn-variant($white, $white, $twitter, $gray-darker);
}

.button-google {
  @include btn-variant($white, $white, $google, $gray-darker);
}

// Btn with shadow
.button-shadow{
  > span {
    box-shadow: 0 3px 11px 0 rgba(0, 0, 0, 0.15);
  }

  &:hover{
    > span {
      box-shadow: 0 3px 5px 0 rgba(0, 0, 0, 0.15);
    }
  }
}


// Button Sizes
// --------------------------------------------------

.button-xs {
  // btn-size($padding-vertical, $padding-horizontal, $font-size, $line-height, $border-radius)
  @include btn-size(7px, 30px, 14px, 24px, 0);
}

.button-sm {
  // btn-size($padding-vertical, $padding-horizontal, $font-size, $line-height, $border-radius)
  font-size: 18px;
}

.button-md {
  // btn-size($padding-vertical, $padding-horizontal, $font-size, $line-height, $border-radius)
  @include btn-size(13px, 30px, 18px, 25px, 0);
}

.button-lg {
  // btn-size($padding-vertical, $padding-horizontal, $font-size, $line-height, $border-radius)
  @include btn-size(16px, 30px, 18px, 25px, 0);
}


// Button Shapes
// --------------------------------------------------

.button-circle {
  border-radius: 25px;

  > span {
    border-radius: 25px;
  }
}

.button-ellipse {
  &-md {
    border-radius: 5px;

    > span {
      border-radius: 5px;
    }
  }
}


// Button Icon styles
// --------------------------------------------------

.button {
  &.button-icon {

    &:before {
      position: absolute;
      top: 50%;
      display: inline-block;
      text-align: center;
      vertical-align: middle;
      font-size: 24px;
      width: 24px;
      line-height: 1;
      transform: translateY(-60%);
    }

    &-left {
      padding-right: 25px;
      padding-left: 45px;
      text-align: right;

      &:before {
        left: 10px;
      }
      
      > span {
        &:before {
          left: -25px;
          right: calc(100% - 30px);
        }
      }

      &:hover,
      &:focus,
      &:active,
      &:hover {
        > span:before {
          left: -25px;
          right: 20px;
        }
      }
    }

    &-right {
      padding-left: 25px;
      padding-right: 45px;
      text-align: left;

      &:before{
        right: 10px;
      }

      > span {
        &:before {
          left: calc(100% - 50px);
        }
      }
    }
  }

  &.button-icon-xxs {

    &:before {
      font-size: 16px;
      transform: translateY(-50%);
    }
  }

  &.button-icon-xs {

    &:before {
      font-size: 18px;
      transform: translateY(-50%);
    }
  }

  &.button-icon-sm {

    &:before {
      font-size: 20px;
      width: 26px;
    }
  }
}

.button-icon {
  &.button-lg {
    min-width: 192px;
    padding-left: 35px;
  }
}

.button-extra {
  padding: 46px 60px 46px 20px;
  font-size: 22px;
  line-height: 41px;
  min-height: 137px;
  width: 100%;

  > span {
    transition: 300ms all ease-in-out;

    .button-skew__item,
    &:after {
      position: absolute;

      top: 0;
      right: 0;
      bottom: 0;
    }
    
    &:before {
      left: auto;
      right: 0;
      width: 100px;
      transform: skewX(47deg);
      background: $primary-sec;
      z-index: auto;
    }

    &:after {
      content: '';
      width: 36px;
      transform-origin: 0 0;
      transform: skewX(16deg);
      background: $gray-darker;
    }

    .button-skew__item {
      width: 40px;
      transform-origin: 100% 100%;
      transform: skewX(-7deg);
      background: $white;
    }
  }
  
  &:hover {
    > span {
      background: $gray-darker;
    }
  }

  @media (min-width: $screen-xs-min) {
    font-size: 30px;
    padding-right: 80px;
    padding-left: 40px;

    > span {

      &:before {
        width: 125px;
      }

      &:after {
        width: 56px;
      }

      .button-skew__item {
        width: 60px;
      }
    }
  }
}

.button-group-vertical {
  @include display-flex();
  @include flex-direction(column);
  @include align-items(center);

  * + .button {
    margin-top: 10px;
  }
}

.group-sm-right {
  
  @media (min-width: $screen-sm-min) {
    @include align-items(flex-end);
  }
}