/*
*
* Posts
* --------------------------------------------------
*/


.post {

  h4,
  h6 {
    a {
      @include link($gray-darker, $primary);
    }
  }

  time {
    display: inline-block;
    font-family: $font-family-sec;
  }

  .icon-link,
  .author-name {
    font-weight: 700;
    font-family: $font-family-base;
  }

  .group-xl > * { vertical-align: middle; }

  * + time { margin-top: 18px; }
}

// post mini

.post-mini {
  text-align: left;

  time {
    display: block;
  }

  time + * {
    margin-top: 3px;
  }
}

.post-footer {

  a {
    font-family: inherit;
    font-weight: 700;
  }

  & + & {
    margin-top: 25px;
  }
}

.post-sidebar {

  h6 {
    font-weight: 400;

    a {
      @include link($gray-darker, $primary);
    }
  }

  .unit__left { padding-top: 5px; }

  & + & {
    margin-top: 20px;
  }
}

// post justify

.post-justify {
  color: $gray-darker;

  .link-position {
    right: 0;
    bottom: 2px;
  }

  .post-link {
    font-size: 60px;
    line-height: 48px;
  }

  * + time { margin-top: 16px; }
  * + .product-characteristics { margin-top: 16px; }

  @media (min-width: $screen-sm-min) {
    .post-content {
      max-width: 340px;
    }

    .link-position {
      right: 35px;
    }
  }

  @media (min-width: $screen-md-min) {
    .post-content {
      max-width: 500px;
    }
  }
}

.post-justify-2 {
  h1 {
    max-width: 750px;
    margin-left: auto;
    margin-right: auto;
  }

  time {
    font-weight: 700;
    font-family: $font-family-sec;
  }

  * + h1 { margin-top: 8px; }

  @media (min-width: $screen-sm-min) {
    h1 {
      line-height: 1.25;
    }
  }
}

.post-link {
  display: inline-block;
}

.post-panel {
  time {
    font-weight: 700;
    font-family: $font-family-base;
  }

  a {
    font-weight: 700;
    font-family: $font-family-base;
  }
}

.post-panel-justify {
  @include display-flex();
  @include align-items(center);
  @include flex-wrap(wrap);
  @include justify-content(center);

  > * {
    flex-basis: 100%;
  }

  @media (min-width: $screen-xs-min) {
    @include justify-content(space-between);

    > * {
      flex-basis: auto;
    }
  }
}

.post-panel-white {
  a {
    @include link($white, $primary);
  }
}

.post-panel-gray {
  a {
    @include link($gray-light, $primary);
  }

  > p {
    color: $gray-darker;

    a {
      @include link($primary,$gray-darker);
    }
  }
}

.post-panel-item,
.icon-link {
  > * {
    display: inline-block;
    vertical-align: middle;
  }

  > * + * {
    margin-left: 7px;
  }
}

/*
*
* Posts masonry
* --------------------------------------------------
*/

.post-masonry {
  position: relative;
  padding: 40px 25px 40px 35px;
  max-width: 350px;
  margin-left: auto;
  margin-right: auto;

  time {
    display: inline-block;
    font-size: 18px;
    font-family: $font-family-sec;
    width: 100%;
  }

  .post-link {
    font-size: 48px;
    line-height: 38px;
  }

  * + time { margin-top: 12px; }

  * + .post-link { margin-top: 18px; }
  * + .post-link.link-position { margin-top: 0; }

  @media (min-width: $screen-xs-min) {
    padding-left: 45px;
  }

  @media (min-width: $screen-sm-min) {
    margin-left: 0;
    margin-right: 0;
    max-width: 100%;
  }

  @media (min-width: $screen-md-min) {
    padding-top: 20px;
    padding-right: 0;
    padding-left: 35px;
  }
  
  @media (min-width: $screen-lg-min) {
    padding-top: 20px;
    padding-right: 25px;
    padding-left: 45px;

    &.inset-right-lg-40 {
      padding-top: 40px;
    }
  }

  @media (min-width: $screen-xlg-min) {
    padding-top: 40px
  }
}

.post-masonry-content {
  position: relative;
  z-index: 1;
}

.post-masonry-long {
  min-height: 697px;
}

.post-masonry-short {
  min-height: 333px;
}

.post-content-white {
  color: $white;

  h4 a {
    @include link($white, $white);

    &:hover {
      text-decoration: underline;
    }
  }
}

.post-content-dark {

  h4 a {
    @include link($gray-darker, $gray-darker);

    &:hover {
      text-decoration: underline;
    }
  }
}

.bg-post-primary {
  background-color: $primary;
}

.bg-post-primary-sec {
  background-color: $primary-sec;
}

/*
*
* Post variant 1
* --------------------------------------------------
*/

.post-variant-1 {

  .post-title {
    padding: 34px 15px 22px 0;
    border-top: 1px solid $gray-2;
  }

  .post-content {
    padding: 20px 15px 36px;
    border-bottom: 1px solid $gray-2;
  }

  .post-title,
  .post-content {
    position: relative;

    &:before,
    &:after {
      position: absolute;
      content: '';
      width: 1px;
      background: $gray-2;
    }

    &:before {
      left: 0;
    }

    &:after {
      right: 0;
    }
  }

  .post-title {
    &:before {
      top: 0;
      height: 25px;
    }

    &:after {
      top: 0;
      bottom: 15px;
    }
  }

  .post-content {

    &:before,
    &:after {
      bottom: 0;
      top: 30px;
    }
  }
  
  .box-skew {

    &:after {
      display: none;
      width: 47px;
      bottom: 0;
      right: 0;
      background: $primary;
      transform: skewX(-8deg);
      transform-origin: 100% 100%;

    }
  }

  .icon-link {
    @include link($gray-darker, $primary-sec);
  }

  * + .group-xl { margin-top: 20px; }

  @media (min-width: $screen-sm-min) {
    h6 {
      max-width: 90%;
    }

    .box-skew {

      &:after {
        display: block;
      }
    }
  }

  @media (min-width: $screen-md-min) {
    .post-content {
      padding-left: 30px;
      padding-right: 30px;
    }

    h6 {
      max-width: 100%;
    }
  }

  @media (min-width: $screen-lg-min) {
    h6 {
      max-width: 90%;
    }
  }
}

/*
*
* Post variant 2
* Post variant 3
* --------------------------------------------------
*/

.post-variant-2,
.post-variant-3 {
  @extend %context-dark;

  .post-img {
    position: relative;
    overflow: hidden;
    will-change: transform;

    &:before {
      position: absolute;
      content: '';
      top: 0;
      right: -24px;
      bottom: 0;
      left: -24px;
      background-repeat: no-repeat;
      background-size: cover;
      will-change: transform;
      z-index: 1;
    }
  }

  time {
    display: block;
    font-family: $font-family-sec;
  }

  .post-content {
    time {
      font-weight: 700;
    }
  }

  h6,
  h4 {
    a {
      @include link($white, $primary);
    }
  }

  * + p {
    margin-top: 12px;
  }

  * + .button {
    margin-top: 18px;
  }

  @media (min-width: $screen-sm-min) {
    .post-img {
      transform: skewX(-6deg);
      margin-left: -1px;
      outline: 1px solid transparent;
      -webkit-backface-visibility: hidden;
      
      &:before {
        transform: skewX(6deg);
      }
    }
  }
}

/*
*
* Post variant 2
* --------------------------------------------------
*/

.post-variant-2 {
  position: relative;

  .post-img {
    height: 330px;

    &:after {
      position: absolute;
      content: '';
      top: 0;
      right: 0;
      bottom: 0;
      left: 0;
      background: rgba($gray-base, .5);
      z-index: 1;
      transition: all 300ms;
    }
  }

  .post-content-overlay {
    position: absolute;
    top: 50%;
    left: 50%;
    width: 100%;
    max-width: 300px;
    will-change: transform;
    transform: translate(-50% , -50%);
    transition: all 300ms;
    z-index: 1;
  }

  h4 { font-size: 22px; }

  @media (min-width: $screen-sm-min) {
    .bg-post-6,
    .bg-post-7 { transform-origin: 100% 100%; }

    .bg-post-8,
    .bg-post-9{ transform-origin: 0 0; }

    &.post-first {
      .post-content-overlay {
        transform: translate(-40% , -50%);
      }
    }

    &.post-last {
      .post-content-overlay {
        transform: translate(-55% , -50%);
      }
    }
  }

  @media (min-width: $screen-xs-min) {
    .post-img {
      height: 360px;
    }
  }

  @media (min-width: $screen-sm-min) {
    .post-img {
      height: 400px;
    }
  }

  @media (min-width: $screen-lg-min) {
    .post-img {
      transform-origin: 50% 50%;
    }

    .post-content-overlay {
      max-width: 225px;
    }
  }

  @media (min-width: $screen-xlg-min) {
    .post-content-overlay {
      max-width: 300px;
    }

    .post-img {
      height: 483px;
    }

    h4 {
      font-size: 30px;
    }
  }
}

.desktop {
  .post-variant-2 {

    .post-img {

      &:after {
        opacity: 0;
        visibility: hidden;
      }
    }

    .post-content-overlay {
      opacity: 0;
      visibility: hidden;
    }

    &:hover {
      .post-content-overlay,
      .post-img:after {
        display: block;
        opacity: 1;
        visibility: visible;
      }
    }
  }
}

/*
*
* Post variant 3
* --------------------------------------------------
*/

.post-variant-3 {
  position: relative;
  text-align: center;

  .post-content {
    position: absolute;
    top: 50%;
    left: 50%;
    width: 100%;
    max-width: 270px;
    will-change: transform;
    transform: translate(-50% , -50%);
    transition: all 300ms;
    z-index: 1;
  }

  .post-img {
    height: 300px;
  }

  .author-name,
  .icon-link {
    @include link($white, $primary);
  }

  h4 {
    font-size: 22px;
  }

  time + h4 { margin-top: 8px; }
  time + h6 { margin-top: 10px; }
  h4 + .group-xl { margin-top: 20px; }
  h6 + .group-xl { margin-top: 30px; }

  @media (min-width: $screen-lg-min) {

    .post-content {
      max-width: 350px;
    }

    .post-img {
      height: 400px;
    }

    h4 {
      font-size: 30px;
    }
  }
  
  @media (min-width: $screen-sm-min) {
    .post-img {
      height: 350px;
    }

    .bg-post-10,
    .bg-post-11 { transform-origin: 100% 100%; }

    .bg-post-12,
    .bg-post-13 { transform-origin: 0 0; }
  }

  @media (min-width: $screen-md-min) {
    .post-img {
      transform-origin: 50% 50%;
    }
  }

  @media (min-width: $screen-xlg-min) {

    .post-content {
      max-width: 400px;
    }

    .post-img {
      height: 483px;
    }
  }
}

.post-variant-3-custom {

  .post-content {
    max-width: 230px;
  }

  .post-img {
    transform: skewX(-5deg);
    margin-left: -1px;

    &:before {
      transform: skewX(5deg);
    }
  }

  @media (min-width: $screen-xs-min) {
    .post-content {
      max-width: 250px;
    }

    .post-img {
      transform: skewX(-6deg);

      &:before {
        transform: skewX(6deg);
      }
    }
  }

  @media (min-width: $screen-sm-min) {
    .post-img {
      height: 270px;
    }
  }
  
  @media (min-width: $screen-lg-min) {
    .post-content {
      max-width: 270px;
    }
  }
}

/*
*
* Post minimal
* --------------------------------------------------
*/
.post-minimal {

  .link {
    display: inline-block;
    font-weight: 700;
    @include link($primary-sec, $gray-darker);
  }

  h4 {
    a {
      @include link($gray-darker, $primary-sec);
    }
  }

  * + .link { margin-top: 15px; }

  @media (min-width: $screen-md-min) {
    h4 { font-size: 28px; }
  }

  @media (min-width: $screen-lg-min) {
    h4 {
      font-size: 30px;
      max-width: 90%;
    }
  }
}

.post-minimal-2,
.post-minimal-3 {

  h1 {
    line-height: 1.25;
    font-size: 30px;
  }

  @media (min-width: $screen-sm-min) {
    .button {
      position: absolute;
      bottom: -20px;
    }

    * + .button { margin-top: 0; }
  }
  
  @media (min-width: $screen-lg-min) {
    h1 { font-size: 48px; }
  }
}

.post-minimal-2 {

  h1 {

    a {
      @include link($white, $gray-darker);
    }
  }

  h6,
  time {
    font-weight: 400;
  }

  * + .product-characteristics { margin-top: 30px; }

  @media (min-width: $screen-sm-min) {
    padding-left: 40px;
  }
  
  @media (min-width: $screen-md-min) {
    padding-left: 85px;

    h1 + h6 { margin-top: 24px; }
  }
}

.post-minimal-3 {
  h1 {

    a {
      @include link($white, $primary);
    }
  }

  time {
    display: inline-block;
  }

  h1 + time { margin-top: 24px; }
  time + p { margin-top: 30px; }
}

/*
*
* Post skew
* --------------------------------------------------
*/

.post-skew-left-top {

  &:before,
  &:after {
    top: 0;
    left: 0;
    transform-origin: 0 0;
  }
}

.post-skew-right-top {
   &:before,
   &:after {
     top: 0;
     right: 0;
     transform-origin: 0 0;
   }
 }

.post-skew-right-bottom {
  &:before,
  &:after {
    bottom: 0;
    right: 0;
    transform-origin: 100% 100%;
  }
}

.post-skew-left-bottom {
  &:before,
  &:after {
    bottom: 0;
    left: 0;
    transform-origin: 100% 100%;
  }
}

.post-skew-var-1 {

  &:before {
    width: 154px;
    transform: skewX(-54deg);
    background-color: $gray-darker;
  }

  &:after {
    width: 72px;
    transform: skewX(-14deg);
    background-color: $primary;
  }
}

.post-skew-var-2 {

  &:before {
    width: 100px;
    transform: skewX(-19deg);
    background-color: $primary-sec;
  }

  &:after {
    width: 130px;
    transform: skewX(-35deg);
    background-color: $gray-darker;
  }
}

.post-skew-var-3 {

  &:before {
    width: 64px;
    transform: skewX(-11deg);
    background-color: $white;
  }

  &:after {
    width: 78px;
    transform: skewX(-21deg);
    background-color: $gray-darker;
  }
}

.post-skew-var-4 {

  &:before {
    width: 196px;
    transform: skewX(49deg);
    background-color: $primary;
  }

  &:after {
    width: 57px;
    transform: skewX(27deg);
    background-color: $gray-darker;
  }
}

.post-skew-var-5 {

  &:before {
    width: 76px;
    transform: skewX(-13deg);
    background-color: $white;
  }

  &:after {
    width: 121px;
    transform: skewX(-32deg);
    background-color: $gray-darker;
  }
}

.post-skew-var-6 {

  &:before {
    width: 110px;
    transform: skewX(-19deg);
    background-color: $white;
  }

  &:after {
    width: 149px;
    transform: skewX(-36deg);
    background-color: $gray-darker;
  }
}

.post-skew-var-7 {

  &:before {
    width: 158px;
    transform: skewX(59deg);
    background-color: $white;
  }

  &:after {
    width: 80px;
    transform: skewX(27deg);
    background-color: $primary;
  }
}

.post-skew-var-8 {

  &:before {
    width: 80px;
    transform: skewX(-56deg);
    background-color: $white;
  }

  &:after {
    width: 45px;
    transform: skewX(-14deg);
    background-color: $primary;
  }

  @media (min-width: $screen-xs-min) {
    &:before {
      width: 120px;
    }

    &:after {
      width: 50px;
    }
  }

  @media (min-width: $screen-xlg-min) {

    &:before {
      width: 150px;
    }

    &:after {
      width: 70px;
    }
  }
}

.link-position {
  position: absolute;
  z-index: 3;
}

.link-dark {
  @include link($gray-darker, $white);

  &.link-position {
    bottom: 35px;
  }
}

.link-primary {
  @include link($primary, $white);

  &.link-position {
    bottom: 30px;
  }
}

.link-primary-sec-2 {
  @include link($primary-sec, $white);

  &.link-position {
    bottom: 40px;
  }
}

.link-primary-sec {
  @include link($primary-sec, $gray-darker);
}

.link-white {
  @include link($white, $gray-darker);
}

.link-right {
  right: 18px;
}

/*
*
* Video
* --------------------------------------------------
*/

.post-video {
  position: relative;
  overflow: hidden;
  text-align: center;

  .bg-video {
    outline: 1px solid transparent;
    -webkit-backface-visibility: hidden;
  }

  time { display: inline-block; }
}

.post-video__image {
  position: absolute;
  top: -2px;
  right: -160px;
  bottom: -2px;
  left: -160px;
  background: $gray-base;

  img {
    position: absolute;
    top: 50%;
    left: 60%;
    transform: translate(-60%, -50%);
    width: 100%;
    min-width: 100%;
    max-width: none;
    height: auto;
    min-height: 101%;
    opacity: 1;
  }

  @media (min-width: $screen-xs-min) {
    right: -120px;
    left: -120px;
  }

  @media (min-width: $screen-sm-min) {
    right: -2px;
    left: -2px;
  }
}

.post-video__image-2 {
  width: 100%;

  img {
    width: 100%;
  }
}

.post-video__caption {
  width: 100%;
  padding-left: 15px;
  padding-right: 15px;
  min-height: 200px;

  .heading-6,
  h6 {
    font-weight: 400;
  }

  h1 + h6  { margin-top: 13px; }

  h4 + time { margin-top: 5px; }

  @media (min-width: $screen-xs-min) {
    max-width: 90%;
  }
}

.post-video__body {
  position: relative;
  z-index: 2;
  @include display-flex;
  @include flex-direction(column);
  @include flex-wrap(nowrap);
  @include align-items(center);
  @include justify-content(center);

  &:before {
    content: '';
  }
}

.post-video__control-2 {
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  display: inline-block;
  width: 50px;
  height: 35px;
  background: url(../images/button-play.png) no-repeat 50% 50%;
  background-size: cover;

  &:hover {
    transform: translate(-50%, -50%) scale(1.1);
  }

  @media (min-width: $screen-sm-min) {
    width: 68px;
    height: 47px;
  }
}

* + .post-video-subtitle {
  margin-top: 10px;
}

.post-video-var-1 {
  padding-top: 70px;
  padding-bottom: 70px;
}

.post-video-var-2,
.post-video-var-6,
.post-video-var-7 {
  padding-top: 70px;
  padding-bottom: 70px;

  .bg-video {
    position: absolute;
    overflow: hidden;
    content: '';
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    transform-origin: 0 0;

    &:before {
      position: absolute;
      content: '';
      top: 0;
      right: -30px;
      bottom: 0;
      left: -30px;
      background-repeat: no-repeat;
      background-size: cover;
      will-change: transform;
      z-index: 1;
    }
  }

  @media (min-width: $screen-lg-min) {
    h1 {
      font-size: 40px;
    }

    .bg-video {

      &:before {
        background-position: 100% 50%;
      }
    }
  }

  @media (min-width: $screen-xlg-min) {
    h1 {
      font-size: 48px;
    }
  }
}

.post-video-var-2 {

  @media (min-width: $screen-lg-min) {

    .bg-video {
      transform: skewX(-6deg);

      &:before {
        transform: skewX(6deg);
      }
    }
  }
}

.post-video-var-3 {
  padding-top: 70px;
  padding-bottom: 70px;
}

.post-video-var-4 {
  padding-top: 70px;
  padding-bottom: 70px;

  .post-video__caption {
    min-height: auto;
  }
}

.post-video-var-5 {
  text-align: left;

  * + h6 { margin-top: 20px; }
  * + p { margin-top: 34px; }
}

.post-video-var-6 {

  .post-video__caption {
    max-width: 100%;
  }

  @media (min-width: $screen-lg-min) {

    .post-video__caption {
      max-width: 75%;
    }

    .bg-video {
      transform: skewX(-6deg);

      &:before {
        transform: skewX(6deg);
      }
    }
  }
}

.post-video-var-7 {

  .post-video__caption {
    max-width: 100%;
  }

  @media (min-width: $screen-lg-min) {

