/*
*
* Post Classic
* --------------------------------------------------
*/

.post-classic {

  time {
    font-weight: 700;
  }

  h6 {
    a {
      @include link($gray-darker, $primary);
    }
  }

  .post-classic-text {

    .link-arrow { display: inline-block; }
    > span { margin-right: 10px; }
  }

  .unit .unit {
    margin-top: 5px;
    text-align: center;
  }

  .author-name {
    font-weight: 700;
    display: inline-block;
  }

  &:hover {
    a img {
      transform: scale(1.05);
    }
  }

  @media (min-width: $screen-sm-min) {
    .author-name {
      max-width: 65px;
    }
  }
}

.post-classic-body {
  > .unit {
    @include align-items(center);
  }

  @media (min-width: $screen-sm-min) {
    > .unit {
      @include align-items(flex-start);
    }
  }
}

.post-classic-img-wrap {
  a {
    display: inline-block;
    overflow: hidden;
  }

  img {
    will-change: transform;
    transition: 600ms linear transform;
  }
}

.blog-item {

  h6 + * { margin-top: 16px; }

  .list-marked {
    > li + li { margin-top: 10px; }
  }

  .post-sidebar {

    h6 {
      max-width: 170px;
    }
  }

  @media (min-width: $screen-lg-min) {
    .list-tags {
      max-width: 250px;
    }
  }
}

/*
*
* Post grid
* --------------------------------------------------
*/

.post-grid {
  position: relative;
  text-align: left;

  .post-grid-header {
    position: absolute;
    top: 18px;
    left: 10px;

    a {
      @include link($primary, $white);
    }
  }

  .post-grid-footer {
    position: absolute;
    left: 10px;
    bottom: 18px;

    max-width: 250px;
    color: $white;

    a {
      @include link($white, $primary);
    }
  }

  .post-link {
    font-size: 48px;
    line-height: 38px;
  }

  .post-panel {
    font-weight: 700;
  }

  .link-position {
    right: 5px;
    bottom: 10px;
  }

  img { width: 100%; }

  h6 + .post-panel {
    margin-top: 8px;
  }

  @media (min-width: $screen-xs-min) {

    .post-grid-header {
      left: 25px;
    }

    .post-grid-footer {
      left: 25px;
      bottom: 26px;
    }


    .link-position {
      bottom: 15px;
    }
  }

  @media (min-width: $screen-md-min) {
    .post-grid-footer {
      max-width: 300px;
    }
  }

  @media (min-width: $screen-xlg-min) {
    .post-grid-footer {
      max-width: 400px;
    }

    .link-position {
      right: 18px;
      bottom: 27px;
    }
  }

  @media (min-width: 1880px) {
    .post-grid-footer {
      max-width: 470px;
    }
  }
}

/*
*
* Post modern
* --------------------------------------------------
*/

.post-modern {
  border: 1px solid $gray-2;

  h6 {
    a {
      @include link($gray-darker, $primary);
    }
  }

  .post-modern-inset { padding-top: 18px; }

  .post-panel-item {
    font-weight: 700;
  }

  * + .post-panel { margin-top: 16px; }
  * + h6 { margin-top: 12px; }

  & + & { margin-top: 30px; }

  &:hover {
    a img {
      transform: scale(1.05);
    }
  }

  @media (max-width: $screen-xs-max) {
    .post-modern-img-wrap {

      a,
      img {
        width: 100%;
      }
    }
  }
}

.post-modern-1 {

  .soundcloud-player-classic {
    padding: 15px 15px 0;

    @media (min-width: $screen-sm-min) {
      padding: 30px 30px 0;
    }
  }

  .post-modern-inset {
    padding: 30px 15px;

    @media (min-width: $screen-sm-min) {
      padding-left: 30px;
      padding-right: 30px;
    }
  }
}

.post-modern-2 {

  .soundcloud-player-classic {
    padding: 15px 15px 0;

    @media (min-width: $screen-md-min) {
      padding: 30px 30px 0;
    }
  }

  .post-modern-inset {
    padding: 30px 15px;

    @media (min-width: $screen-md-min) {
      padding-left: 30px;
      padding-right: 30px;
    }
  }
}

.post-modern-wrap {
  position: relative;
  overflow: hidden;

  @media (min-width: $screen-sm-min) {
    padding-left: 100px;
    margin-right: 100px;
  }
}

.post-modern-header {

  padding-top: 30px;
  padding-bottom: 20px;

  @media (min-width: $screen-sm-min) {
    position: absolute;
    top: 26px;
    left: 0;
    width: 70px;
    padding-top: 0;
  }

  & + .post-modern-content {

    .soundcloud-player-classic {
      padding-top: 0;
    }
  }
}

.post-modern-img-wrap {
  a {
    display: inline-block;
    overflow: hidden;
  }

  img {
    will-change: transform;
    transition: 600ms linear transform;
  }
}

.decor-vertical {

  &:before {
    display: none;
    position: absolute;
    content: '';
    top: 0;
    left: 50%;
    transform: translateX(-50%);
    will-change: transform;
    height: 1000px;
    width: 1px;
    background: $gray-lighter;
  }

  @media (min-width: $screen-sm-min) {
    position: absolute;
    top: 0;
    left: 0;
    width: 70px;
    padding-top: 0;

    &:before {
      display: block;
    }
  }
}

.post-timeline-vertical {

  &:before,
  &:after {
    display: none;
    position: absolute;
    content: '';
    left: 50%;
    transform: translateX(-50%);
    will-change: transform;
    height: 1000px;
    width: 1px;
    background: $gray-lighter;
  }

  &:before {
    top: calc( 100% + 7px );
  }

  &:after {
    bottom: calc( 100% + 7px );
  }

  @media (min-width: $screen-sm-min) {
    &:before,
    &:after {
      display: block;
    }
  }
}

.post-timeline-right {

  &:after {
    display: none;
    position: absolute;
    content: '';
    top: 50%;
    left: calc(100% + 7px);
    transform: translateY(-50%);
    will-change: transform;
    background: $gray-lighter;
    height: 1px;
    width: 16px;
  }

  @media (min-width: $screen-sm-min) {
    &:after {
      display: block;
    }
  }
}

.post-modern-date {

  time {
    display: inline-block;
    min-width: 70px;
    font-weight: 700;
    text-align: center;
  }
}

* + .post-modern-date { margin-top: 5px; }
.post-modern-date + * { margin-top: 5px; }

