// Boxes
// --------------------------------------------------

// Text block





// Text block

.box-skew {
  position: relative;
  overflow: hidden;

  > .box-skew__item,
  &:before,
  &:after {
    position: absolute;
    content: ' ';
    height: 100%;
    will-change: trasform;
    outline: 1px solid transparent;
    -webkit-backface-visibility: hidden;
  }
}

// Box Skew Var 1

  // Breadcrumbs

.box-skew-var-1 {

  &:before {
    bottom: 0;
    right: 0;
    height: 170px;
    width: 50px;
    background: $primary;
    transform: skewX(-27deg);
    transform-origin: 100% 100%;
  }

  &:after {
    top: 0;
    right: 0;
    height: 180px;
    width: 100px;
    background: $gray-base;
    transform: skewX(41deg);
    transform-origin: 0 0;
  }

  > .box-skew__item {
    top: 0;
    right: 0;
    height: 183px;
    width: 40px;
    background: $white;
    transform: skewX(17deg);
    transform-origin: 0 0;
    z-index: 1;
  }

  @media (min-width: $screen-sm-min) {
    &:before {
      width: 84px;
    }

    &:after {
      width: 150px;
    }

    > .box-skew__item {
      width: 73px;

    }
  }
}


// Box Skew Var 2

  // Swiper

.box-skew-var-2 {

  &:before {
    bottom: 0;
    right: 0;
    height: 100%;
    width: 70px;
    background: $primary;
    transform: skewX(-26deg);
    transform-origin: 100% 100%;
  }

  &:after {
    top: 0;
    right: 0;
    height: 100%;
    width: 80px;
    background: $gray-base;
    transform: skewX(40deg);
    transform-origin: 0 0;
  }

  > .box-skew__item {
    top: 0;
    right: 0;
    height: 100%;
    width: 35px;
    background: $white;
    transform: skewX(18deg);
    transform-origin: 0 0;
    z-index: 1;
  }

  @media (min-width: $screen-xs-min) {

    &:before {
      width: 100px;
    }
    
    &:after {
      width: 100px;
    }

    > .box-skew__item {
      width: 40px;
    }
  }

  @media (min-width: $screen-sm-min) {
    &:before {
      width: 130px;
    }

    &:after {
      width: 144px;
    }

    > .box-skew__item {
      width: 45px;

    }
  }

  @media (min-width: $screen-md-min) {
    &:before {
      width: 160px;
    }
  }
}

// Section-skew

.section-skew {
  position: relative;
  overflow: hidden;

  &.section-skew-custom {
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    z-index: -1;
  }

  .shell {
    position: relative;
  }

  > .section-skew__item,
  &:before,
  &:after {
    position: absolute;
    content: ' ';
    height: 100%;
    will-change: trasform;
    z-index: 2;
    outline: 1px solid transparent;
    -webkit-backface-visibility: hidden;
  }
}

.skew-4-elem {
  &:before,
  &:after {
    right: 0;
  }

  > .section-skew__item {
    &:nth-child(1),
    &:nth-child(2) {
      left: 0;
    }
  }
}

.skew-5-elem {
  > .section-skew__item:nth-child(1),
  &:before,
  &:after {
    right: 0;
  }

  > .section-skew__item {
    &:nth-child(2),
    &:nth-child(3) {
      left: 0;
    }
  }
}

.skew-6-elem {
  > .section-skew__item:nth-child(1),
  &:before,
  &:after {
    right: 0;
  }

  > .section-skew__item {
    &:nth-child(2),
    &:nth-child(3),
    &:nth-child(4) {
      left: 0;
    }
  }
}

