.counter-wrap {
  position: relative;

  .bg-skew {
    position: absolute;
    overflow: hidden;
    top: 0;
    right: 8px;
    bottom: 0;
    left: 8px;
    transform: skewX(-6deg);
    will-change: transform;
    outline: 1px solid transparent;
    -webkit-backface-visibility: hidden;
    z-index: -1;

    &:before {
      position: absolute;
      content: '';
      top: 0;
      right: 0;
      bottom: 0;
      left: 85%;
      transform: skewX(15deg);
      transform-origin: 0 0;
      outline: 1px solid transparent;
      -webkit-backface-visibility: hidden;
      transition: .3s all ease-in-out;
      will-change: transform;
      z-index: -1;
    }
  }
}

.box-skew__counter-var-1 {

  .bg-skew {
    background: $primary;
    &:before {
      background: $gray-darker;
    }
  }
}

.box-skew__counter-var-2 {

  .bg-skew {
    background: $primary-sec;
    &:before {
      background: $gray-darker;
    }
  }
}

.box-skew__counter-var-3 {

