    }
  }
}

.box-skew__counter-var-4 {

  .bg-skew {
    background: $gray-light;
    &:before {
      background: $gray-darker;
    }
  }
}

// Border
.box-border-md-right {
  
  @media (min-width: $screen-md-min) {
    > div {
      position: relative;

      &:last-child {
        &:after {
          display: none;
        }
      }

      &:after {
        position: absolute;
        content: '';
        top: 0;
        right: 15px;
        bottom: 0;
        width: 1px;
        background: rgba($white, .1);
        transform: translateY(15px);
      }
    }
  }
}


// Newsletter
.box-newsletter {
