// Box Comment

.box-comment > .box-comment {
  margin-left: 0;
  margin-top: 40px;

  @media (min-width: $screen-sm-min) {
    margin-left: 100px;
    margin-top: 30px;
  }
}

.box-comment {

  .time-comment {
    font-weight: 700;
    font-family: $font-family-sec;
  }

  .author-name {
    font-weight: 700;
    @include link($gray-darker, $primary);
  }

  .unit__left { padding-top: 4px; }

  * + p { margin-top: 6px; }
  * + .group-lg { margin-top: 5px; }
}

.link-arrow {
  font-weight: 700;

  &:after {
    display: inline-block;
    vertical-align: middle;
    content: '\e317';
    font-size: 18px;
    font-weight: 400;
    font-family: "Material Icons";
    transform: scale(-1, 1);
    will-change: transform;
    margin-left: 14px;
  }
}

.form-comment {

  .group-middle {
    span {
      font-weight: 700;
      font-family: $font-family-sec;
    }

    * + .button {
      margin-top: 10px;
    }

    @media (min-width: $screen-xs-min) {
      * + .button {
        margin-top: 0;
      }
    }

    @media (max-width: $screen-xxs-max) {
      @include justify-content(center);
    }
  }

  * + .group-middle  { margin-top: 10px; }
}

h5 + .box-comment { margin-top: 25px; }
* + .form-comment { margin-top: 30px; }

@media (min-width: $screen-sm-min) {
  h5 + .box-comment { margin-top: 36px; }
}


.box-offsets-custom-1 {
  h5 + p { margin-top: 16px; }
  p + p { margin-top: 20px; }
}

.box-contacts {
  font-weight: 700;

  a { font-family: $font-family-base; }

  .address {
    max-width: 130px;
  }

  .tel {

    a {
      @include link(inherit, $primary);
    }
  }

  .mail {

    a {
      @include link($primary-sec, $gray-darker);
    }
  }

  p + p { margin-top: 0; }
}

* + .box-contacts { margin-top: 18px; }

.box-xs-center {
  max-width: 70%;
  margin-left: auto;
  margin-right: auto;
  
  @media (min-width: $screen-xs-min) {
    max-width: 100%;
  }
}