//
// Table custom
// --------------------------------------------------

$table-head-padding:            12px 28px;
$table-cell-padding:            12px 28px;
$table-condensed-cell-padding:  5px;

$table-bg:                      $white;
$table-bg-accent:               $gray-2;
$table-bg-hover:                $primary;
$table-bg-active:               $table-bg-hover;

$table-border-color:            #ebebeb;

.table-custom {
  box-sizing: border-box;
  width: 100%;
  max-width: 100%;
  font-weight: 400;
  letter-spacing: 0;
  text-align: left;
  background: $table-bg;
  border: 1px solid $table-border-color;

  thead {
    font-size: 1.28em;
  }

  th,
  td {
    background: $table-bg;
  }

  th {
    padding: 10px 24px;
    font-weight: 700;
    text-transform: none;

    @media (min-width: $screen-xs-min) {
      padding-top: 20px;
      padding-bottom: 20px;
    }

    @media (min-width: $screen-sm-min) {
      padding: 20px 40px;
    }

    @media (min-width: $screen-md-min) {
      padding: $table-head-padding;
    }
  }

  td {
    padding: 10px 24px;
    border-bottom: 1px solid $table-border-color;

    @media (min-width: $screen-xs-min) {
      padding-top: 20px;
      padding-bottom: 20px;
    }

    @media (min-width: $screen-sm-min) {
      padding: 20px 40px;
    }

    @media (min-width: $screen-md-min) {
      padding: $table-cell-padding;
    }
  }

  tfoot td {
    color: $gray-darker;
  }
}

// Styles
.table-custom-secondary {
  th {
    color: $white;
    background: $primary;
  }
}

.table-custom-primary {
  th {
    color: $white;
    background: $primary;
  }
}

.table-custom-gray-light {
  thead th {
    color: $gray-darker;
    background: $table-bg-accent;
  }

  td + td {
    border-left: none;
  }
}

.table-custom-bordered {
  border: 1px solid $table-border-color;
  td {
    border: 1px solid $table-border-color;
  }

  tbody > tr:first-of-type > td {
    border-top: 0;
  }
}

.table-custom-striped {
  tbody tr:nth-of-type(odd) td {
    background: transparent;
  }

  tbody tr:nth-of-type(even) td {
    background: $gray-lighter;
  }

  tbody td {
    border: 0;
  }
}

// Modificators
.table-round {
  position: relative;
  border-radius: 4px;
  overflow: hidden;
}

.table-custom-responsive {
  overflow-x: auto;
  min-height: 0.01%;

  @media screen and (max-width: $screen-xs-max) {
    width: 100%;
    margin-bottom: 10px;
    overflow-y: hidden;
    -ms-overflow-style: -ms-autohiding-scrollbar;
    border: 1px solid $table-border-color;

    // Tighten up spacing
    > .table-custom {
      // Ensure the content doesn't wrap
      > thead,
      > tbody,
      > tfoot {
        > tr {
          > th,
          > td {
            white-space: nowrap;
          }
        }
      }
    }
  }
}

.table-product {
  text-align: left;

  tr {
    vertical-align: baseline;
  }

  tr td:nth-child(2) {
    color: $gray-darker;
  }
}

.table-product-1 {
  td {
    padding-top: 2px;
    padding-bottom: 2px;
  }

  tr td:nth-child(1) {
    min-width: 154px;
  }
}

.table-product-2 {
  td {
    padding-top: 5px;
    padding-bottom: 5px;
  }

  tr td:nth-child(1) {
    min-width: 134px;
  }
}

.table-product-3 {
  
  font-size: 14px;

  td {
    padding-top: 4px;
    padding-bottom: 5px;
  }

  tr td:nth-child(1) {
    min-width: 140px;
  }
  
  @media (min-width: $screen-sm-min) {
    font-size: 18px;

    tr td:nth-child(1) {
      min-width: 170px;
    }
  }
}

* + .table-product {
  margin-top: 30px;
}

.table-sm-center {
  margin-left: auto;
  margin-right: auto;

  @media (min-width: $screen-md-min) {
    margin-left: 0;
    margin-right: 0;
  }
}

//table cart
.table-shopping-cart {

  tbody,
  tfoot {
    font-size: 1.28em;
  }

  .link {
    font-weight: 400;
    font-family: inherit;
    @include link(inherit, $primary);
  }

  th {
    padding: 21px 14px
  }

  td {
    min-width: 0;

    span + span { margin-left: 14px; }

    > * {
      vertical-align: middle;
    }
  }

  thead {

    th {

      &:nth-child(3) {
        text-align: center;
      }

      &:first-child {
        padding-left: 65px;
      }
    }
  }

  tbody {

    td {
      padding: 6px 14px;

      &:first-child {
        width: 67px;
        padding-left: 18px;
      }

      &:nth-child(2) {
        padding-left: 0;
        width: 19%;
        min-width: 85px;
      }

      &:nth-child(3) {
        width: 43%;
        min-width: 250px;
      }

      &:nth-child(4) {
        width: 7%;
      }

      &:nth-child(5) {
        width: 10%;
        text-align: center;
      }

      &:last-of-type {
        min-width: 120px;
      }
    }

    @media (min-width: $screen-md-min) {
      td {
        &:last-of-type {
          min-width: 183px;
        }
      }
    }
  }

  tfoot {
    td {

      padding: 21px 14px;

      &:first-child {
        padding-left: 65px;
        font-weight: 700;
      }

      &:nth-child(3) {
        span:first-of-type {

          color: $gray-light;
        }
      }
    }
  }
}

// table checkout
.table-checkout {

  .shiping {
    font-size: 14px;
    font-weight: 400;
    line-height: 20px;
  }

  .price-dark {
    color: $gray-dark;
  }

  td {
    width: 50%;
  }

  tr {

    &:first-child {
      font-size: 1.28em;
      font-weight: 700;
      color: $gray-darker;
    }

    &:last-child {
      color: $gray-darker;

      .price { font-weight: 700; }
    }

    &:nth-child(odd) {
      > * {
        background: rgba($gray-2, 1);
      }
    }
  }

  .link {
    font-weight: 700;
    font-family: $font-family-base;
    @include link($gray-darker, $primary);
  }

  @media (min-width: $screen-md-min) {
    td {
      padding: 14px 25px;
    }

    tr {

      &:first-child {

        td {
          padding-top: 12px;
          padding-bottom: 12px;
        }
      }
    }
  }
}