// Panel
.rdc-panel{
  @include display-flex();
  @include align-items(center);
  @include justify-content(space-between);
}

.rdc-panel + .rdc-table { margin-top: 30px; }

.wrap-calendar {
  @include display-flex();

  .heading-4 {
    margin: 0 0 0 10px;
  }
}

// Controls
.rdc-next, .rdc-prev, .rdc-events_close{
  color: $gray;
  font: 400 18px/38px "FontAwesome";
  cursor: pointer;
  transition: .3s all ease;
  
  &:hover {
    color: $primary;
  }
}

.rdc-next{
  &:before {
    content: "\f054";
  }
}

.rdc-prev{
  &:before {
    content: "\f053";
  }
}

.rdc-events_close{
  position: absolute;
  left: 10px;
  top: 0;

  &:before {
    content: '\f00d';
  }
}

// Events
.rdc-events{
  position: absolute;
  top: 50%;
  left: 50%;
  background: white;
  padding: 30px 40px 20px;
  box-shadow: 0 0 12px 1px rgba(0, 0, 0, 0.25);
  transform: translate(-50%, -50%);
  opacity: 0;
  visibility: hidden;
  transition: .4s all ease-in-out;
  min-width: 280px;

  @media (min-width: $screen-sm-min) {
    padding: 30px 60px 20px;
  }

  .rdc-event {
    color: $gray;
  }

  .rd-calendar.show-events & {
    opacity: 1;
    visibility: visible;
  }
  
  @media (min-width: $screen-md-min) {
    display: none;
  }
}

.rdc-event{
  font-size: 0;
  visibility: hidden;
  opacity: 0;
  transition: .4s all ease-in-out;

  &.active {
    visibility: visible;
    opacity: 1;
    font-size: inherit;
  }
}

// Table
.rdc-table{
  position: relative;
  width: 100%;
  table {
    border-spacing: 1px;
    border-collapse: separate;
    margin-left: auto;
    margin-right: auto;
  }

  td {
    position: relative;
    height: 60px;
    width: auto;

    @media (min-width: $screen-sm-min) {
      height: 100px;
      width: 100px;
    }

    @media (min-width: $screen-md-min) {
      width: 133px;
      height: 142px;
    }
    
    @media (min-width: $screen-lg-min) {
      width: 166px;
      height: 152px;
    }
  }

  tr:first-child {
    display: none;
  }
}

.rdc-month{

}

.rdc-fullyear{

}

.rdc-table_day{
  color: $gray;
  padding: 5px 0;
  text-transform: uppercase;
  font-size: 18px;
}

.rdc-table_date{
  position: absolute;
  top: 0;
  bottom: 0;
  right: 0;
  left: 0;
  font-weight: 400;
  font-family: $font-family-base;
  font-size: 18px;
  line-height: 1;
  padding: 10px;
  color: $gray-light;
  border: 1px solid $gray-2;
  background-color: $gray-2;
  box-sizing: border-box;
  
  @media (min-width: $screen-sm-min) {
    text-align: left;  
  }

  @media (min-width: $screen-lg-min) {
    padding: 13px 16px;
  }
}

.ie-10,
.ie-11 {
  .rdc-table_date{
    height: 60px;

    @media (min-width: $screen-sm-min) {
      height: 100px;
    }

    @media (min-width: $screen-md-min) {
      height: 142px;
    }

    @media (min-width: $screen-lg-min) {
      height: 152px;
    }
  }
}

.rdc-table_today{
  /*background-color: $gray-light;*/
}

.rdc-table_event{
  color: $white;
  display: none;
  
  @media (min-width: $screen-md-min) {
    display: block;
  }
}

.rdc-table_next,
.rdc-table_prev{
  background: $white;
}

.rdc-table_events-count {
  position: relative;
  padding-right: 60px;
  max-width: 70px;
  margin: 0 auto;
  color: $white;
  display: none;
}

.rdc-table_events {
  text-align: center;
  font-size: 18px;
  max-width: 140px;
  margin-left: auto;
  margin-right: auto;
  font-family: $font-family-base;

  @media (min-width: $screen-md-min) {
    position: relative;

    &:before {
      display: none;
    }
  }

  &:before {
    position: absolute;
    top: 50%;
    left: 50%;
    -webkit-transform: translate(-50%, 0);
    -ms-transform: translate(-50%, 0);
    transform: translate(-50%, 0);
    content: '\f00c';
    font-family: 'FontAwesome';
    color: $white;
    font-size: 20px;
  }
}

.rdc-table_has-events {
  .rdc-table_date {
    color: $white;
    background-color: $primary;
    border: 1px solid $primary;
  }
  
  @media (min-width: $screen-md-min) {
    pointer-events: none;

    .rdc-table_date {
      pointer-events: none;
    }
  }
}




















