/*
*
* Offsets
* --------------------------------------------------
*/

// Element offset
* + p { margin-top: 15px; }
p + p { margin-top: 12px; }


// Headings
h1 + h2 { margin-top: 9px; }
h2 + h3 { margin-top: 8px; }
h3 + h4 { margin-top: 10px; }
h4 + h5 { margin-top: 11px; }
h5 + h6 { margin-top: 11px; }
h1 + h6 { margin-top: 16px; }
h1 + p { margin-top: 30px; }


// Headings + Paragraph
h2 + p { margin-top: 26px; }
h3 + p { margin-top: 28px; }
h4 + p { margin-top: 32px; }
h5 + p { margin-top: 30px; }
h6 + p { margin-top: 18px; }


// Paragraph + Headings
p + h2 { margin-top: 45px; }
p + h3 { margin-top: 45px; }
p + h4 { margin-top: 45px; }
p + h5 { margin-top: 2px; }
p + h6 { margin-top: 45px; }


// Tags + Tags
img + p { margin-top: 15px; }
ul + p { margin-top: 10px; }
h3 + img { margin-top: 42px; }
h6 + img { margin-top: 22px; }
img + h6 { margin-top: 20px; }


// Tags + Classes
h1 + .row { margin-top: 35px; }
h6 + .quote-wrap { margin-top: 20px; }
h6 + [class*='group-buttons'] { margin-top: 10px; }
h6 + .rd-mailform { margin-top: 16px; }
h6 + .post-panel { margin-top: 35px; }
p + .rd-mailform { margin-top: 20px; }
p + .list-marked { margin-top: 10px; }
p + .list-ordered { margin-top: 10px; }
h5 + .list-marked-none { margin-top: 16px; }


// Classes
* + .rd-mailform { margin-top: 30px; }
* + .button { margin-top: 35px; }
* + .wrap-button { margin-top: 35px; }
* + .offset-top-0 { margin-top: 0px; }
* + .offset-top-16 { margin-top: 16px; }
* + .tabs-custom { margin-top: 37px; }
* + .panel-custom-group { margin-top: 37px; }
* + [class*='group-buttons'] { margin-top: 22px; }
* + .pagination-wrap { margin-top: 30px; }
* + .list-tags{ margin-top: 10px; }


// Classes + Tags
.range + h3 { margin-top: 80px; }
.range + h5 { margin-top: 30px; }
.rd-mailform + h6 { margin-top: 16px; }
.post-video + h6 { margin-top: 30px; }


// Classes + Classes
.shell + .shell { margin-top: 60px; }
.range + .range { margin-top: 60px; }
.section-skew__item + .range { margin-top: 0; }
.big + .list-marked { margin-top: 30px; }
.grid-masonry + .wrap-button { margin-top: 25px; }
.table-product + .group-buttons { margin-top: 32px; }
.heading-group + .owl-carousel { margin-top: 35px; }
.blog-item + .blog-item { margin-top: 35px; }
.post-classic + .post-classic { margin-top: 35px; }
.post-classic-img-wrap + .post-classic-body { margin-top: 20px; }

// Media offsets

@media (min-width: $screen-sm-min) {

}

@media (min-width: $screen-md-min) {
  h1 + h6 { margin-top: 30px; }
}

@media (min-width: $screen-lg-min) {

}

html {
  * + .offset-top-20 { margin-top: 20px; }
  * + .offset-top-22 { margin-top: 22px; }


  // Range spacing
  .range-60 {
    @include responsive-offset(60px);
  }

  .range-50 {
    @include responsive-offset(50px);
  }

  .range-40 {
    @include responsive-offset(40px);
  }

  .range-30 {
    @include responsive-offset(30px);
  }

  .range-25 {
    @include responsive-offset(25px);

    @media (max-width: $screen-xs-max) {
      > .offset-xs-top-10 {
        margin-top: 10px;
      }
    }
  }

  .range-20 {
    @include responsive-offset(20px);
  }

  .range-15 {
    @include responsive-offset(15px);
  }

  .range-0 {
    @include responsive-offset(0px);
  }

  @media (min-width: $screen-sm-min) {
    .range-sm-0 {
      @include responsive-offset-media(0px);
    }
  }

  @media (min-width: $screen-md-min) {
    .range-md-30 {
      @include responsive-offset-media(30px);
    }
  }

  @media (min-width: $screen-lg-min) {
    .range-lg-30 {
      @include responsive-offset-media(30px);
    }
  }
}