/*
*
* Contexts
* --------------------------------------------------
*/


%context-dark {
  color: $white;

  #{headings()} {
    color: $white;

    a {
      @include link(inherit, $primary);
    }
  }

  .list-primary-sec {

    a {
      &:hover {
        color: $white;
      }
    }
  }

  .product-category-title {
    a {

      &:hover {
        color: $gray-darker;
      }
    }
  }
}

%context-light {
  #{headings()} {
    color: $headings-color;
  }
}

.context-dark {
  @extend %context-dark;
}

.context-light {
  @extend %context-light;
}

%buttons-styles {

  > span {
    position: absolute;
    overflow: hidden;
    top: 0;
    right: 3px;
    bottom: 0;
    left: 3px;
    will-change: transform;

    &:before {
      position: absolute;
      content: '';
      top: 0;
      right: 0;
      bottom: 0;
      transform-origin: 0 0;
      will-change: transform;
      transition: .3s all ease-in-out;
    }
  }

  &:hover,
  &:focus,
  &:active,
  &:hover {

    > span:before {
      left: 0;
    }
  }

  &.button-circle,
  &.button-ellipse-md,
  &.button-square {
    > span {
      transform: skewX(0deg);
      left: 0;
      right: 0;

      &:before {
        transform: skewX(16deg);
      }
    }
  }

  &.button-circle {
    &:hover,
    &:focus,
    &:active,
    &:hover {
      > span:before {
        left: 10%;
      }
    }
  }

  &.button-extra {
    &:hover,
    &:focus,
    &:active,
    &:hover {
      > span:before {
        left: auto;
      }
    }
  }
}